import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-country-delete-dialog',
  templateUrl: './country-delete-dialog.component.html',
  styleUrls: ['./country-delete-dialog.component.css']
})
export class CountryDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public countryDeleteDialogRef: MatDialogRef<CountryDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public countryData: any,
    private toastr: ToastrService
  ) { }

  public deleteCountrySubscription: any;

  public btnDeleteCountry() {
    let btnDeleteCountry: Element = document.getElementById("btnDeleteCountry");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteCountry.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteCountry(this.countryData.objCountryModel.Id);
    this.deleteCountrySubscription = this.systemTablesService.deleteCountryObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Country was successfully deleted.", "Success");
          this.countryDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteCountry.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteCountrySubscription != null) this.deleteCountrySubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.countryDeleteDialogRef.close();

    if (this.deleteCountrySubscription != null) this.deleteCountrySubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteCountrySubscription != null) this.deleteCountrySubscription.unsubscribe();
  }

}
