  public addCitizen(): void {
    this.httpClient.post(this.defaultAPIURLHost + "/api/mst/citizen/add", JSON.stringify(""), this.options).subscribe(
      response => {
        let responseResults: string[] = ["success", response.toString()];
        this.addCitizenSubject.next(responseResults);
      },
      error => {
        let errorResults: string[] = ["failed", error["error"]];
        this.addCitizenSubject.next(errorResults);
      }
    );
  }

  public saveCitizen(objCitizenModel: CitizenModel): void {
    this.httpClient.put(this.defaultAPIURLHost + "/api/mst/citizen/save/" + objCitizenModel.Id, JSON.stringify(objCitizenModel), this.options).subscribe(
      response => {
        let responseResults: string[] = ["success", response.toString()];
        this.saveCitizenSubject.next(responseResults);
      },
      error => {
        let errorResults: string[] = ["failed", error["error"]];
        this.saveCitizenSubject.next(errorResults);
      }
    );
  }

