  public saveCitizensChildren(objCitizensChildModel: CitizensChildrenModel): void {
    if (objCitizensChildModel.Id == 0) {
      this.httpClient.post(this.defaultAPIURLHost + "/api/mst/citizen/children/add", JSON.stringify(objCitizensChildModel), this.options).subscribe(
        response => {
          let responseResults: string[] = ["success", response.toString()];
          this.saveCitizensChildrenSubject.next(responseResults);
        },
        error => {
          let errorResults: string[] = ["failed", error["error"]];
          this.saveCitizensChildrenSubject.next(errorResults);
        }
      );
    } else {
      this.httpClient.put(this.defaultAPIURLHost + "/api/mst/citizen/children/update/" + objCitizensChildModel.Id, JSON.stringify(objCitizensChildModel), this.options).subscribe(
        response => {
          let responseResults: string[] = ["success", response.toString()];
          this.saveCitizensChildrenSubject.next(responseResults);
        },
        error => {
          let errorResults: string[] = ["failed", error["error"]];
          this.saveCitizensChildrenSubject.next(errorResults);
        }
      );
    }
  }

