import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-barangay-delete-dialog',
  templateUrl: './barangay-delete-dialog.component.html',
  styleUrls: ['./barangay-delete-dialog.component.css']
})
export class BarangayDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public barangayDeleteDialogRef: MatDialogRef<BarangayDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public barangayData: any,
    private toastr: ToastrService
  ) { }

  public deleteBarangaySubscription: any;

  public btnDeleteBarangay() {
    let btnDeleteBarangay: Element = document.getElementById("btnDeleteBarangay");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteBarangay.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteBarangay(this.barangayData.objBarangayModel.Id);
    this.deleteBarangaySubscription = this.systemTablesService.deleteBarangayObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Barangay was successfully deleted.", "Success");
          this.barangayDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteBarangay.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteBarangaySubscription != null) this.deleteBarangaySubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.barangayDeleteDialogRef.close();

    if (this.deleteBarangaySubscription != null) this.deleteBarangaySubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteBarangaySubscription != null) this.deleteBarangaySubscription.unsubscribe();
  }

}
