import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CardService } from '../card.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-card-delete-dialog',
  templateUrl: './card-delete-dialog.component.html',
  styleUrls: ['./card-delete-dialog.component.css']
})
export class CardDeleteDialogComponent implements OnInit {

  constructor(
    private cardService: CardService,
    public cardDeleteDialogRef: MatDialogRef<CardDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public cardData: any,
    private toastr: ToastrService
  ) { }

  public deleteCardSubscription: any;

  public btnDeleteCard() {
    let btnDeleteCard: Element = document.getElementById("btnDeleteCard");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteCard.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.cardService.deleteCard(this.cardData.objCardModel.Id);
    this.deleteCardSubscription = this.cardService.deleteCardObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Card was successfully deleted.", "Success");
          this.cardDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteCard.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteCardSubscription != null) this.deleteCardSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.cardDeleteDialogRef.close();

    if (this.deleteCardSubscription != null) this.deleteCardSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteCardSubscription != null) this.deleteCardSubscription.unsubscribe();
  }
}
