import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-blood-type-delete-dialog',
  templateUrl: './blood-type-delete-dialog.component.html',
  styleUrls: ['./blood-type-delete-dialog.component.css']
})
export class BloodTypeDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public bloodTypeDeleteDialogRef: MatDialogRef<BloodTypeDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public bloodTypeData: any,
    private toastr: ToastrService
  ) { }

  public deleteBloodTypeSubscription: any;

  public btnDeleteBloodType() {
    let btnDeleteBloodType: Element = document.getElementById("btnDeleteBloodType");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteBloodType.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteBloodType(this.bloodTypeData.objBloodTypeModel.Id);
    this.deleteBloodTypeSubscription = this.systemTablesService.deleteBloodTypeObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Blood type was successfully deleted.", "Success");
          this.bloodTypeDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteBloodType.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteBloodTypeSubscription != null) this.deleteBloodTypeSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.bloodTypeDeleteDialogRef.close();

    if (this.deleteBloodTypeSubscription != null) this.deleteBloodTypeSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteBloodTypeSubscription != null) this.deleteBloodTypeSubscription.unsubscribe();
  }

}
