import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BloodTypeModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-blood-type-detail-dialog',
  templateUrl: './blood-type-detail-dialog.component.html',
  styleUrls: ['./blood-type-detail-dialog.component.css']
})
export class BloodTypeDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public bloodTypeDetailDialogRef: MatDialogRef<BloodTypeDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public bloodTypeData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Blood Type Detail";

  public bloodTypeModel: BloodTypeModel = {
    Id: this.bloodTypeData.objBloodTypeModel.Id,
    BloodType: this.bloodTypeData.objBloodTypeModel.BloodType
  }

  public saveBloodTypeSubscription: any;

  public btnSaveBloodType(): void {
    let btnSaveBloodType: Element = document.getElementById("btnSaveBloodType");
    let btnCloseBloodTypeDialog: Element = document.getElementById("btnCloseBloodTypeDialog");

    btnSaveBloodType.setAttribute("disabled", "disabled");
    btnCloseBloodTypeDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveBloodType(this.bloodTypeModel);
    this.saveBloodTypeSubscription = this.systemTablesService.saveBloodTypeObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Blood type was successfully saved.", "Success");
          this.bloodTypeDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveBloodType.removeAttribute("disabled");
          btnCloseBloodTypeDialog.removeAttribute("disabled");
        }

        if (this.saveBloodTypeSubscription != null) this.saveBloodTypeSubscription.unsubscribe();
      }
    );
  }

  public btnCloseBloodTypeDialog(): void {
    this.bloodTypeDetailDialogRef.close();

    if (this.saveBloodTypeSubscription != null) this.saveBloodTypeSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.bloodTypeData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveBloodTypeSubscription != null) this.saveBloodTypeSubscription.unsubscribe();
  }

}
