import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-province-delete-dialog',
  templateUrl: './province-delete-dialog.component.html',
  styleUrls: ['./province-delete-dialog.component.css']
})
export class ProvinceDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public provinceDeleteDialogRef: MatDialogRef<ProvinceDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public provinceData: any,
    private toastr: ToastrService
  ) { }

  public deleteProvinceSubscription: any;

  public btnDeleteProvince() {
    let btnDeleteProvince: Element = document.getElementById("btnDeleteProvince");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteProvince.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteProvince(this.provinceData.objProvinceModel.Id);
    this.deleteProvinceSubscription = this.systemTablesService.deleteProvinceObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Province was successfully deleted.", "Success");
          this.provinceDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteProvince.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteProvinceSubscription != null) this.deleteProvinceSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.provinceDeleteDialogRef.close();

    if (this.deleteProvinceSubscription != null) this.deleteProvinceSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteProvinceSubscription != null) this.deleteProvinceSubscription.unsubscribe();
  }

}
