import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-region-delete-dialog',
  templateUrl: './region-delete-dialog.component.html',
  styleUrls: ['./region-delete-dialog.component.css']
})
export class RegionDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public regionDeleteDialogRef: MatDialogRef<RegionDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public regionData: any,
    private toastr: ToastrService
  ) { }

  public deleteRegionSubscription: any;

  public btnDeleteRegion() {
    let btnDeleteRegion: Element = document.getElementById("btnDeleteRegion");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteRegion.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteRegion(this.regionData.objRegionModel.Id);
    this.deleteRegionSubscription = this.systemTablesService.deleteRegionObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Region was successfully deleted.", "Success");
          this.regionDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteRegion.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteRegionSubscription != null) this.deleteRegionSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.regionDeleteDialogRef.close();

    if (this.deleteRegionSubscription != null) this.deleteRegionSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteRegionSubscription != null) this.deleteRegionSubscription.unsubscribe();
  }

}
