import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

import { ServicesService } from './services.service';
import { ToastrService } from 'ngx-toastr';

import {
  ServiceDepartmentModel,
  ServiceGroupModel,
  ServiceModel
} from './services.model';

import { ServiceDepartmentDetailDialogComponent } from './service-department-detail-dialog/service-department-detail-dialog.component';
import { ServiceDepartmentDeleteDialogComponent } from './service-department-delete-dialog/service-department-delete-dialog.component';
import { ServiceGroupDetailDialogComponent } from './service-group-detail-dialog/service-group-detail-dialog.component';
import { ServiceGroupDeleteDialogComponent } from './service-group-delete-dialog/service-group-delete-dialog.component';
import { ServiceDetailDialogComponent } from './service-detail-dialog/service-detail-dialog.component';
import { ServiceDeleteDialogComponent } from './service-delete-dialog/service-delete-dialog.component';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.css']
})
export class ServicesComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public serviceDepartmentDetailDialog: MatDialog,
    public serviceDepartmentDeleteDialog: MatDialog,
    public serviceGroupDetailDialog: MatDialog,
    public serviceGroupDeleteDialog: MatDialog,
    public serviceAddDialog: MatDialog,
    public serviceEditDialog: MatDialog,
    public serviceDeleteDialog: MatDialog,
    private toastr: ToastrService
  ) { }

  public listServiceDepartmentSubscription: any;
  public listServiceDepartmentObservableArray: ObservableArray = new ObservableArray();
  public listServiceDepartmentCollectionView: CollectionView = new CollectionView(this.listServiceDepartmentObservableArray);
  public listServiceDepartmentPageIndex: number = 15;
  public serviceDepartmentModel: ServiceDepartmentModel = new ServiceDepartmentModel();

  public listServiceGroupSubscription: any;
  public listServiceGroupObservableArray: ObservableArray = new ObservableArray();
  public listServiceGroupCollectionView: CollectionView = new CollectionView(this.listServiceGroupObservableArray);
  public listServiceGroupPageIndex: number = 15;
  public serviceGroupModel: ServiceGroupModel = new ServiceGroupModel();

  public listServiceSubscription: any;
  public listServiceObservableArray: ObservableArray = new ObservableArray();
  public listServiceCollectionView: CollectionView = new CollectionView(this.listServiceObservableArray);
  public listServicePageIndex: number = 15;
  public serviceModel: ServiceModel = new ServiceModel();

  public addServiceSubscription: any;
  public detailServiceSubscription: any;

  public listServiceDepartmentData(): void {
    this.listServiceDepartmentObservableArray = new ObservableArray();
    this.listServiceDepartmentCollectionView = new CollectionView(this.listServiceDepartmentObservableArray);
    this.listServiceDepartmentCollectionView.pageSize = 15;
    this.listServiceDepartmentCollectionView.trackChanges = true;
    this.listServiceDepartmentCollectionView.refresh();

    this.servicesService.listServiceDepartment();
    this.listServiceDepartmentSubscription = this.servicesService.listServiceDepartmentObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listServiceDepartmentObservableArray = data;
          this.listServiceDepartmentCollectionView = new CollectionView(this.listServiceDepartmentObservableArray);
          this.listServiceDepartmentCollectionView.pageSize = this.listServiceDepartmentPageIndex;
          this.listServiceDepartmentCollectionView.trackChanges = true;
          this.listServiceDepartmentCollectionView.refresh();
        }

        if (this.listServiceDepartmentSubscription != null) this.listServiceDepartmentSubscription.unsubscribe();
      }
    );
  }

  public btnAddServiceDepartment(): void {
    this.serviceDepartmentModel = new ServiceDepartmentModel();

    const serviceDepartmentDetailDialogRef = this.serviceDepartmentDetailDialog.open(ServiceDepartmentDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Add Service Department",
        objServiceDepartmentModel: this.serviceDepartmentModel
      },
      disableClose: true
    });

    serviceDepartmentDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceDepartmentData();
      }
    });
  }

  public btnEditServiceDepartment(): void {
    let currentEditItem = this.listServiceDepartmentCollectionView.currentItem;
    this.serviceDepartmentModel.Id = currentEditItem.Id;
    this.serviceDepartmentModel.ServiceDepartment = currentEditItem.ServiceDepartment;
    this.serviceDepartmentModel.OfficerInCharge = currentEditItem.OfficerInCharge;
    this.serviceDepartmentModel.ContactNumber = currentEditItem.ContactNumber;
    this.serviceDepartmentModel.EmailAddress = currentEditItem.EmailAddress;

    const serviceDepartmentDetailDialogRef = this.serviceDepartmentDetailDialog.open(ServiceDepartmentDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Edit Service Department",
        objServiceDepartmentModel: this.serviceDepartmentModel
      },
      disableClose: true
    });

    serviceDepartmentDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceDepartmentData();
      }
    });
  }

  public btnDeleteServiceDepartment(): void {
    let currentEditItem = this.listServiceDepartmentCollectionView.currentItem;
    this.serviceDepartmentModel.Id = currentEditItem.Id;

    const serviceDepartmentDeleteDialogRef = this.serviceDepartmentDeleteDialog.open(ServiceDepartmentDeleteDialogComponent, {
      width: '300px',
      data: { objServiceDepartmentModel: this.serviceDepartmentModel },
      disableClose: true
    });

    serviceDepartmentDeleteDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceDepartmentData();
      }
    });
  }

  public listServiceGroupData(): void {
    this.listServiceGroupObservableArray = new ObservableArray();
    this.listServiceGroupCollectionView = new CollectionView(this.listServiceGroupObservableArray);
    this.listServiceGroupCollectionView.pageSize = 15;
    this.listServiceGroupCollectionView.trackChanges = true;
    this.listServiceGroupCollectionView.refresh();

    this.servicesService.listServiceGroup();
    this.listServiceGroupSubscription = this.servicesService.listServiceGroupObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listServiceGroupObservableArray = data;
          this.listServiceGroupCollectionView = new CollectionView(this.listServiceGroupObservableArray);
          this.listServiceGroupCollectionView.pageSize = this.listServiceGroupPageIndex;
          this.listServiceGroupCollectionView.trackChanges = true;
          this.listServiceGroupCollectionView.refresh();
        }

        if (this.listServiceGroupSubscription != null) this.listServiceGroupSubscription.unsubscribe();
      }
    );
  }

  public btnAddServiceGroup(): void {
    this.serviceGroupModel = new ServiceGroupModel();

    const serviceGroupDetailDialogRef = this.serviceGroupDetailDialog.open(ServiceGroupDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Add Service Group",
        objServiceGroupModel: this.serviceGroupModel
      },
      disableClose: true
    });

    serviceGroupDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceGroupData();
      }
    });
  }

  public btnEditServiceGroup(): void {
    let currentEditItem = this.listServiceGroupCollectionView.currentItem;
    this.serviceGroupModel.Id = currentEditItem.Id;
    this.serviceGroupModel.ServiceGroup = currentEditItem.ServiceGroup;
    this.serviceGroupModel.Description = currentEditItem.Description;
    this.serviceGroupModel.ServiceDepartmentId = currentEditItem.ServiceDepartmentId;

    const serviceGroupDetailDialogRef = this.serviceGroupDetailDialog.open(ServiceGroupDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Edit Service Group",
        objServiceGroupModel: this.serviceGroupModel
      },
      disableClose: true
    });

    serviceGroupDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceGroupData();
      }
    });
  }

  public btnDeleteServiceGroup(): void {
    let currentEditItem = this.listServiceGroupCollectionView.currentItem;
    this.serviceGroupModel.Id = currentEditItem.Id;

    const serviceGroupDeleteDialogRef = this.serviceGroupDeleteDialog.open(ServiceGroupDeleteDialogComponent, {
      width: '300px',
      data: { objServiceGroupModel: this.serviceGroupModel },
      disableClose: true
    });

    serviceGroupDeleteDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceGroupData();
      }
    });
  }

  public listServiceData(): void {
    this.listServiceObservableArray = new ObservableArray();
    this.listServiceCollectionView = new CollectionView(this.listServiceObservableArray);
    this.listServiceCollectionView.pageSize = 15;
    this.listServiceCollectionView.trackChanges = true;
    this.listServiceCollectionView.refresh();

    this.servicesService.listService();
    this.listServiceSubscription = this.servicesService.listServiceObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listServiceObservableArray = data;
          this.listServiceCollectionView = new CollectionView(this.listServiceObservableArray);
          this.listServiceCollectionView.pageSize = this.listServicePageIndex;
          this.listServiceCollectionView.trackChanges = true;
          this.listServiceCollectionView.refresh();
        }

        if (this.listServiceSubscription != null) this.listServiceSubscription.unsubscribe();
      }
    );
  }

  public btnAddService(): void {
    let btnAddService: Element = document.getElementById("btnAddService");
    btnAddService.setAttribute("disabled", "disabled");

    this.servicesService.addService();
    this.addServiceSubscription = this.servicesService.addServiceObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.info("Service was successfully created as draft.", "Info");
          this.getServiceDetail(parseInt(data[1]));

        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnAddService.removeAttribute("disabled");
        }

        if (this.addServiceSubscription != null) this.addServiceSubscription.unsubscribe();
      }
    );
  }

  public getServiceDetail(id: number): void {
    let btnAddService: Element = document.getElementById("btnAddService");

    this.servicesService.detailService(id);
    this.detailServiceSubscription = this.servicesService.detailServiceObservable.subscribe(
      data => {
        if (data != null) {
          this.listServiceData();

          this.serviceModel.Id = data.Id;
          this.serviceModel.Service = data.Service;
          this.serviceModel.Description = data.Description;
          this.serviceModel.ServiceDepartmentId = data.ServiceDepartmentId;
          this.serviceModel.ServiceGroupId = data.ServiceGroupId;
          this.serviceModel.DateEncoded = new Date(data.DateEncoded);
          this.serviceModel.DateExpiry = new Date(data.DateExpiry);
          this.serviceModel.LimitAmount = data.LimitAmount;
          this.serviceModel.IsMultipleUse = data.IsMultipleUse;
          this.serviceModel.StatusId = data.StatusId;
          this.serviceModel.IsLocked = data.IsLocked;

          const serviceAddDialogRef = this.serviceAddDialog.open(ServiceDetailDialogComponent, {
            width: '500px',
            data: {
              objDialogTitle: "Add Service",
              objServiceModel: this.serviceModel
            },
            disableClose: true
          });

          serviceAddDialogRef.afterClosed().subscribe(result => {
            if (result == 200) {
              this.listServiceData();
            }
          });
        }

        if (this.detailServiceSubscription != null) this.detailServiceSubscription.unsubscribe();
        btnAddService.removeAttribute("disabled");
      }
    );
  }

  public btnEditService(): void {
    let currentService = this.listServiceCollectionView.currentItem;
    this.serviceModel.Id = currentService.Id;
    this.serviceModel.Service = currentService.Service;
    this.serviceModel.Description = currentService.Description;
    this.serviceModel.ServiceDepartmentId = currentService.ServiceDepartmentId;
    this.serviceModel.ServiceGroupId = currentService.ServiceGroupId;
    this.serviceModel.DateEncoded = new Date(currentService.DateEncoded);

    if (currentService.DateExpiry != null) {
      this.serviceModel.DateExpiry = new Date(currentService.DateExpiry);
    } else {
      this.serviceModel.DateExpiry = null;
    }

    this.serviceModel.LimitAmount = currentService.LimitAmount;
    this.serviceModel.IsMultipleUse = currentService.IsMultipleUse;
    this.serviceModel.StatusId = currentService.StatusId;
    this.serviceModel.IsLocked = currentService.IsLocked;

    const serviceEditDialogRef = this.serviceEditDialog.open(ServiceDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Edit Service",
        objServiceModel: this.serviceModel
      },
      disableClose: true
    });

    serviceEditDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceData();
      }
    });
  }

  public btnDeleteService(): void {
    let currentEditItem = this.listServiceCollectionView.currentItem;

    if (currentEditItem.IsLocked == true) {
      this.toastr.error("Service cannot be deleted if locked.", "Error");
    } else {
      this.serviceModel.Id = currentEditItem.Id;

      const serviceDeleteDialogRef = this.serviceDeleteDialog.open(ServiceDeleteDialogComponent, {
        width: '300px',
        data: { objServiceModel: this.serviceModel },
        disableClose: true
      });

      serviceDeleteDialogRef.afterClosed().subscribe(result => {
        if (result == 200) {
          this.listServiceData();
        }
      });
    }
  }

  ngOnInit() {
    this.listServiceData();
    this.listServiceGroupData();
    this.listServiceDepartmentData();
  }

  ngOnDestroy() {
    if (this.listServiceDepartmentSubscription != null) this.listServiceDepartmentSubscription.unsubscribe();
    if (this.listServiceGroupSubscription != null) this.listServiceGroupSubscription.unsubscribe();
    if (this.listServiceSubscription != null) this.listServiceSubscription.unsubscribe();
    if (this.addServiceSubscription != null) this.addServiceSubscription.unsubscribe();
    if (this.detailServiceSubscription != null) this.detailServiceSubscription.unsubscribe();
  }
}
