import { Injectable } from '@angular/core';
import { AppSettings } from './../../app-settings';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ObservableArray } from 'wijmo/wijmo';
import { Subject } from 'rxjs';
import { CardModel } from './card.model'

@Injectable({
  providedIn: 'root'
})
export class CardService {

  constructor(
    private appSettings: AppSettings,
    private httpClient: HttpClient
  ) { }

  public defaultAPIURLHost: string = this.appSettings.APIURLHost;
  public options: any = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + localStorage.getItem('access_token')
    })
  };

  public listCardSubject = new Subject<ObservableArray>();
  public listCardObservable = this.listCardSubject.asObservable();
  public searchCitizenListSource = new Subject<ObservableArray>();
  public searchCitizenListObservable = this.searchCitizenListSource.asObservable();
  public listCitizenListSource = new Subject<ObservableArray>();
  public listCitizenListObservable = this.listCitizenListSource.asObservable();
  public listDropdownCardStatusSource = new Subject<ObservableArray>();
  public listDropdownCardStatusObservable = this.listDropdownCardStatusSource.asObservable();
  public saveCardSubject = new Subject<string[]>();
  public saveCardObservable = this.saveCardSubject.asObservable();
  public deleteCardSubject = new Subject<string[]>();
  public deleteCardObservable = this.deleteCardSubject.asObservable();

  public listCard(): void {
    let listCardObservableArray = new ObservableArray();
    this.listCardSubject.next(listCardObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/mst/citizen/card/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listCardObservableArray.push({
              Id: results[i].Id,
              CitizenId: results[i].CitizenId,
              Citizen: results[i].Citizen,
              CardNumber: results[i].CardNumber,
              TotalBalance: results[i].TotalBalance,
              StatusId: results[i].StatusId,
              Status: results[i].Status
            });
          }
        }

        this.listCardSubject.next(listCardObservableArray);
      }
    );
  }

  public listCitizen(): void {
    let listCitizenObservableArray = new ObservableArray();
    this.listCitizenListSource.next(listCitizenObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/mst/citizen/card/citizen/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listCitizenObservableArray.push({
              Id: results[i].Id,
              Surname: results[i].Surname,
              Firstname: results[i].Firstname,
              Middlename: results[i].Middlename,
              Extensionname: results[i].Extensionname,
              Fullname: results[i].Surname + ", " + results[i].Firstname + " " + results[i].Middlename + " " + results[i].Extensionname,
              DateOfBirth: results[i].DateOfBirth,
              PlaceOfBirth: results[i].PlaceOfBirth,
              Sex: results[i].Sex
            });
          }
        }

        this.listCitizenListSource.next(listCitizenObservableArray);
      }
    );
  }

  public searchCitizen(keyword): void {
    let searchCitizenObservableArray = new ObservableArray();
    this.searchCitizenListSource.next(searchCitizenObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/mst/citizen/card/citizen/search?keyword=" + keyword, this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            searchCitizenObservableArray.push({
              Id: results[i].Id,
              Surname: results[i].Surname,
              Firstname: results[i].Firstname,
              Middlename: results[i].Middlename,
              Extensionname: results[i].Extensionname,
              Fullname: results[i].Surname + ", " + results[i].Firstname + " " + results[i].Middlename + " " + results[i].Extensionname,
              DateOfBirth: results[i].DateOfBirth,
              PlaceOfBirth: results[i].PlaceOfBirth,
              Sex: results[i].Sex
            });
          }
        }

        this.searchCitizenListSource.next(searchCitizenObservableArray);
      }
    );
  }

  public listDropdownStatus(): void {
    let listDropdownStatusObservableArray = new ObservableArray();
    this.listDropdownCardStatusSource.next(listDropdownStatusObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/mst/citizen/card/status/dropdown/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listDropdownStatusObservableArray.push({
              Id: results[i].Id,
              Status: results[i].Status
            });
          }
        }

        this.listDropdownCardStatusSource.next(listDropdownStatusObservableArray);
      }
    );
  }

  public saveCard(objCitizensChildModel: CardModel): void {
    if (objCitizensChildModel.Id == 0) {
      this.httpClient.post(this.defaultAPIURLHost + "/api/mst/citizen/card/add", JSON.stringify(objCitizensChildModel), this.options).subscribe(
        response => {
          let responseResults: string[] = ["success", response.toString()];
          this.saveCardSubject.next(responseResults);
        },
        error => {
          let errorResults: string[] = ["failed", error["error"]];
          this.saveCardSubject.next(errorResults);
        }
      );
    } else {
      this.httpClient.put(this.defaultAPIURLHost + "/api/mst/citizen/card/update/" + objCitizensChildModel.Id, JSON.stringify(objCitizensChildModel), this.options).subscribe(
        response => {
          let responseResults: string[] = ["success", response.toString()];
          this.saveCardSubject.next(responseResults);
        },
        error => {
          let errorResults: string[] = ["failed", error["error"]];
          this.saveCardSubject.next(errorResults);
        }
      );
    }
  }

  public deleteCard(id: number): void {
    this.httpClient.delete(this.defaultAPIURLHost + "/api/mst/citizen/card/delete/" + id, this.options).subscribe(
      response => {
        let responseResults: string[] = ["success", ""];
        this.deleteCardSubject.next(responseResults);
      },
      error => {
        let errorResults: string[] = ["failed", error["error"]];
        this.deleteCardSubject.next(errorResults);
      }
    );
  }
}
