import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

import { CardService } from '../card.service';
import { CardModel } from '../card.model';

import { ToastrService } from 'ngx-toastr';

import { CardSearchCitizenDialogComponent } from './card-search-citizen-dialog/card-search-citizen-dialog.component';
import { CardDetailDialogComponent } from '../card-detail-dialog/card-detail-dialog.component';

@Component({
  selector: 'app-card-citizen-dialog',
  templateUrl: './card-citizen-dialog.component.html',
  styleUrls: ['./card-citizen-dialog.component.css']
})
export class CardCitizenDialogComponent implements OnInit {

  constructor(
    private cardService: CardService,
    public cardCitizenDialogRef: MatDialogRef<CardCitizenDialogComponent>,
    public sarchCitizenlDialog: MatDialog,
    public cardDetailDialog: MatDialog
  ) { }

  public listCitizenSubscription: any;
  public listCitizenObservableArray: ObservableArray = new ObservableArray();
  public listCitizenCollectionView: CollectionView = new CollectionView(this.listCitizenObservableArray);
  public listCitizenPageIndex: number = 7;

  public isFilterShow: boolean = false;
  public filterString: string = "";

  public cardModel: CardModel = new CardModel();

  public listCitizenData(): void {
    this.listCitizenObservableArray = new ObservableArray();
    this.listCitizenCollectionView = new CollectionView(this.listCitizenObservableArray);
    this.listCitizenCollectionView.pageSize = 15;
    this.listCitizenCollectionView.trackChanges = true;
    this.listCitizenCollectionView.refresh();

    this.cardService.listCitizen();
    this.listCitizenSubscription = this.cardService.listCitizenListObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listCitizenObservableArray = data;
          this.listCitizenCollectionView = new CollectionView(this.listCitizenObservableArray);
          this.listCitizenCollectionView.pageSize = this.listCitizenPageIndex;
          this.listCitizenCollectionView.trackChanges = true;
          this.listCitizenCollectionView.refresh();
        }

        if (this.listCitizenSubscription != null) this.listCitizenSubscription.unsubscribe();
      }
    );
  }

  public btnCloseCitizenDialog(): void {
    this.cardCitizenDialogRef.close();

    if (this.listCitizenSubscription != null) this.listCitizenSubscription.unsubscribe();
  }

  public btnSearchCitizen(): void {
    const sarchCitizenlDialogRef = this.sarchCitizenlDialog.open(CardSearchCitizenDialogComponent, {
      width: '800px',
      disableClose: true
    });

    sarchCitizenlDialogRef.afterClosed().subscribe(result => {
      if (result != null) {
        if (result[1] != "" || result[1] != null) {
          this.isFilterShow = true;
          this.filterString = result[1];
        }

        this.listCitizenObservableArray = result[0];
        this.listCitizenCollectionView = new CollectionView(this.listCitizenObservableArray);
        this.listCitizenCollectionView.pageSize = this.listCitizenPageIndex;
        this.listCitizenCollectionView.trackChanges = true;
        this.listCitizenCollectionView.refresh();
      }
    });
  }

  public btnClearFilter(): void {
    this.isFilterShow = false;
    this.filterString = "";

    this.listCitizenData();
  }

  public btnPickCitizen(): void {
    let currentEditItem = this.listCitizenCollectionView.currentItem;
    this.cardModel.CitizenId = currentEditItem.Id;
    this.cardModel.Citizen = currentEditItem.Fullname;

    const cardDetailDialogRef = this.cardDetailDialog.open(CardDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Edit Card",
        objCardModel: this.cardModel
      },
      disableClose: true
    });

    cardDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        setTimeout(() => {
          this.cardCitizenDialogRef.close(200);
          if (this.listCitizenSubscription != null) this.listCitizenSubscription.unsubscribe();
        }, 100);
      }
    });
  }

  ngOnInit() {
    this.listCitizenData();

    setTimeout(() => {
      this.btnSearchCitizen();
    }, 100);
  }

  ngOnDestroy() {
    if (this.listCitizenSubscription != null) this.listCitizenSubscription.unsubscribe();
  }
}
