export class ServiceDepartmentModel {
    Id: number = 0;
    ServiceDepartment: string = "";
    OfficerInCharge: string = "";
    ContactNumber: string = "";
    EmailAddress: string = "";
}

export class ServiceGroupModel {
    Id: number = 0;
    ServiceGroup: string = "";
    Description: string = "";
    ServiceDepartmentId: number = 0;
}

export class ServiceModel {
    Id: number = 0;
    Service: string = "";
    Description: string = "";
    ServiceDepartmentId: number = 0;
    ServiceGroupId: number = 0;
    DateEncoded: Date = new Date();
    DateExpiry: Date = new Date();
    LimitAmount: number = 0;
    IsMultipleUse: boolean = false;
    StatusId: number = 0;
    IsLocked: boolean = false;
}