import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ServiceDepartmentModel } from '../services.model'
import { ServicesService } from '../services.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-service-department-detail-dialog',
  templateUrl: './service-department-detail-dialog.component.html',
  styleUrls: ['./service-department-detail-dialog.component.css']
})
export class ServiceDepartmentDetailDialogComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public serviceDepartmentDetailDialogRef: MatDialogRef<ServiceDepartmentDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceDepartmentData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Service Department Detail";

  public serviceDepartmentModel: ServiceDepartmentModel = {
    Id: this.serviceDepartmentData.objServiceDepartmentModel.Id,
    ServiceDepartment: this.serviceDepartmentData.objServiceDepartmentModel.ServiceDepartment,
    OfficerInCharge: this.serviceDepartmentData.objServiceDepartmentModel.OfficerInCharge,
    ContactNumber: this.serviceDepartmentData.objServiceDepartmentModel.ContactNumber,
    EmailAddress: this.serviceDepartmentData.objServiceDepartmentModel.EmailAddress,
  }

  public saveServiceDepartmentSubscription: any;

  public btnSaveServiceDepartment(): void {
    let btnSaveServiceDepartment: Element = document.getElementById("btnSaveServiceDepartment");
    let btnCloseServiceDepartmentDialog: Element = document.getElementById("btnCloseServiceDepartmentDialog");

    btnSaveServiceDepartment.setAttribute("disabled", "disabled");
    btnCloseServiceDepartmentDialog.setAttribute("disabled", "disabled");

    this.servicesService.saveServiceDepartment(this.serviceDepartmentModel);
    this.saveServiceDepartmentSubscription = this.servicesService.saveServiceDepartmentObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Service department was successfully saved.", "Success");
          this.serviceDepartmentDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveServiceDepartment.removeAttribute("disabled");
          btnCloseServiceDepartmentDialog.removeAttribute("disabled");
        }

        if (this.saveServiceDepartmentSubscription != null) this.saveServiceDepartmentSubscription.unsubscribe();
      }
    );
  }

  public btnCloseServiceDepartmentDialog(): void {
    this.serviceDepartmentDetailDialogRef.close();

    if (this.saveServiceDepartmentSubscription != null) this.saveServiceDepartmentSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.serviceDepartmentData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveServiceDepartmentSubscription != null) this.saveServiceDepartmentSubscription.unsubscribe();
  }

}
