import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';

import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

import { CitizenService } from './citizen.service';
import { ToastrService } from 'ngx-toastr';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { CitizenDetailDialogComponent } from './citizen-detail-dialog/citizen-detail-dialog.component';
import { CitizenDeleteDialogComponent } from './citizen-delete-dialog/citizen-delete-dialog.component';
import { CitizenSearchDialogComponent } from './citizen-search-dialog/citizen-search-dialog.component';

import { CitizenModel } from './citizen.model';

@Component({
  selector: 'app-citizen',
  templateUrl: './citizen.component.html',
  styleUrls: ['./citizen.component.css']
})
export class CitizenComponent implements OnInit {

  constructor(
    private citizenService: CitizenService,
    public citizenAddDialog: MatDialog,
    public citizenEditDialog: MatDialog,
    public citizenDeleteDialog: MatDialog,
    public citizenSearchDialog: MatDialog,
    private toastr: ToastrService
  ) { }

  public citizenModel: CitizenModel = new CitizenModel();

  public listCitizenSubscription: any;
  public listCitizenObservableArray: ObservableArray = new ObservableArray();
  public listCitizenCollectionView: CollectionView = new CollectionView(this.listCitizenObservableArray);
  public listCitizenPageIndex: number = 15;

  public isFilterShow: boolean = false;
  public filterString: string = "";

  public addCitizenSubscription: any;
  public detailCitizenSubscription: any;

  public listCitizenData(): void {
    this.listCitizenObservableArray = new ObservableArray();
    this.listCitizenCollectionView = new CollectionView(this.listCitizenObservableArray);
    this.listCitizenCollectionView.pageSize = 15;
    this.listCitizenCollectionView.trackChanges = true;
    this.listCitizenCollectionView.refresh();

    this.citizenService.listCitizen();
    this.listCitizenSubscription = this.citizenService.listCitizenObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listCitizenObservableArray = data;
          this.listCitizenCollectionView = new CollectionView(this.listCitizenObservableArray);
          this.listCitizenCollectionView.pageSize = this.listCitizenPageIndex;
          this.listCitizenCollectionView.trackChanges = true;
          this.listCitizenCollectionView.refresh();
        }

        if (this.listCitizenSubscription != null) this.listCitizenSubscription.unsubscribe();
      }
    );
  }

  public btnAddCitizen(): void {
    let btnAddCitizen: Element = document.getElementById("btnAddCitizen");
    btnAddCitizen.setAttribute("disabled", "disabled");

    this.citizenService.addCitizen();
    this.addCitizenSubscription = this.citizenService.addCitizenObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.info("Citizen was successfully created as draft.", "Info");
          this.getCitizenDetail(parseInt(data[1]));

        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
        }

        if (this.addCitizenSubscription != null) this.addCitizenSubscription.unsubscribe();
      }
    );
  }

  public getCitizenDetail(id: number): void {
    let btnAddCitizen: Element = document.getElementById("btnAddCitizen");

    this.citizenService.detailCitizen(id);
    this.detailCitizenSubscription = this.citizenService.detailCitizenObservable.subscribe(
      data => {
        if (data != null) {
          this.listCitizenData();

          this.citizenModel.Id = data.Id;
          this.citizenModel.Surname = data.Surname;
          this.citizenModel.Firstname = data.Firstname;
          this.citizenModel.Middlename = data.Middlename;
          this.citizenModel.Extensionname = data.Extensionname;
          this.citizenModel.DateOfBirth = new Date(data.DateOfBirth);
          this.citizenModel.PlaceOfBirth = data.PlaceOfBirth;
          this.citizenModel.SexId = data.SexId;
          this.citizenModel.CivilStatusId = data.CivilStatusId;
          this.citizenModel.Height = data.Height;
          this.citizenModel.Weight = data.Weight;
          this.citizenModel.BloodTypeId = data.BloodTypeId;
          this.citizenModel.GSISNumber = data.GSISNumber;
          this.citizenModel.HDMFNumber = data.HDMFNumber;
          this.citizenModel.PhilHealthNumber = data.PhilHealthNumber;
          this.citizenModel.SSSNumber = data.SSSNumber;
          this.citizenModel.TINNumber = data.TINNumber;
          this.citizenModel.AgencyEmployeeNumber = data.AgencyEmployeeNumber;
          this.citizenModel.CitizenshipId = data.CitizenshipId;
          this.citizenModel.TypeOfCitizenshipId = data.TypeOfCitizenshipId;
          this.citizenModel.DualCitizenshipCountry = data.DualCitizenshipCountry;
          this.citizenModel.ResidentialNumber = data.ResidentialNumber;
          this.citizenModel.ResidentialStreet = data.ResidentialStreet;
          this.citizenModel.ResidentialVillage = data.ResidentialVillage;
          this.citizenModel.ResidentialBarangayId = data.ResidentialBarangayId;
          this.citizenModel.ResidentialCityId = data.ResidentialCityId;
          this.citizenModel.ResidentialProvinceId = data.ResidentialProvinceId;
          this.citizenModel.ResidentialZipCode = data.ResidentialZipCode;
          this.citizenModel.PermanentNumber = data.PermanentNumber;
          this.citizenModel.PermanentStreet = data.PermanentStreet;
          this.citizenModel.PermanentVillage = data.PermanentVillage;
          this.citizenModel.PermanentBarangayId = data.PermanentBarangayId;
          this.citizenModel.PermanentCityId = data.PermanentCityId;
          this.citizenModel.PermanentProvinceId = data.PermanentProvinceId;
          this.citizenModel.PermanentZipCode = data.PermanentZipCode;
          this.citizenModel.TelephoneNumber = data.TelephoneNumber;
          this.citizenModel.MobileNumber = data.MobileNumber;
          this.citizenModel.EmailAddress = data.EmailAddress;
          this.citizenModel.OccupationId = data.OccupationId;
          this.citizenModel.EmployerName = data.EmployerName;
          this.citizenModel.EmployerAddress = data.EmployerAddress;
          this.citizenModel.EmployerTelephoneNumber = data.EmployerTelephoneNumber;
          this.citizenModel.SpouseSurname = data.SpouseSurname;
          this.citizenModel.SpouseFirstname = data.SpouseFirstname;
          this.citizenModel.SpouseMiddlename = data.SpouseMiddlename;
          this.citizenModel.SpouseExtensionname = data.SpouseExtensionname;
          this.citizenModel.SpouseOccupationId = data.SpouseOccupationId;
          this.citizenModel.SpouseEmployerName = data.SpouseEmployerName;
          this.citizenModel.SpouseEmployerAddress = data.SpouseEmployerAddress;
          this.citizenModel.FatherSurname = data.FatherSurname;
          this.citizenModel.FatherFirstname = data.FatherFirstname;
          this.citizenModel.FatherMiddlename = data.FatherMiddlename;
          this.citizenModel.FatherExtensionname = data.FatherExtensionname;
          this.citizenModel.MotherSurname = data.MotherSurname;
          this.citizenModel.MotherFirstname = data.MotherFirstname;
          this.citizenModel.MotherMiddlename = data.MotherMiddlename;
          this.citizenModel.MotherExtensionname = data.MotherExtensionname;
          this.citizenModel.PictureURL = data.PictureURL;
          this.citizenModel.StatusId = data.StatusId;
          this.citizenModel.IsLocked = data.IsLocked;

          const citizenAddDialogRef = this.citizenAddDialog.open(CitizenDetailDialogComponent, {
            width: '1200px',
            data: {
              objDialogTitle: "Add Citizen",
              objCitizenModel: this.citizenModel
            },
            disableClose: true
          });

          citizenAddDialogRef.afterClosed().subscribe(result => {
            if (result == 200) {
              this.listCitizenData();
            }
          });
        }

        if (this.detailCitizenSubscription != null) this.detailCitizenSubscription.unsubscribe();
        btnAddCitizen.removeAttribute("disabled");
      }
    );
  }

  public btnEditCitizen(): void {
    let currentCitizen = this.listCitizenCollectionView.currentItem;
    this.citizenModel.Id = currentCitizen.Id;
    this.citizenModel.Surname = currentCitizen.Surname;
    this.citizenModel.Firstname = currentCitizen.Firstname;
    this.citizenModel.Middlename = currentCitizen.Middlename;
    this.citizenModel.Extensionname = currentCitizen.Extensionname;
    this.citizenModel.DateOfBirth = new Date(currentCitizen.DateOfBirth);
    this.citizenModel.PlaceOfBirth = currentCitizen.PlaceOfBirth;
    this.citizenModel.SexId = currentCitizen.SexId;
    this.citizenModel.CivilStatusId = currentCitizen.CivilStatusId;
    this.citizenModel.Height = currentCitizen.Height;
    this.citizenModel.Weight = currentCitizen.Weight;
    this.citizenModel.BloodTypeId = currentCitizen.BloodTypeId;
    this.citizenModel.GSISNumber = currentCitizen.GSISNumber;
    this.citizenModel.HDMFNumber = currentCitizen.HDMFNumber;
    this.citizenModel.PhilHealthNumber = currentCitizen.PhilHealthNumber;
    this.citizenModel.SSSNumber = currentCitizen.SSSNumber;
    this.citizenModel.TINNumber = currentCitizen.TINNumber;
    this.citizenModel.AgencyEmployeeNumber = currentCitizen.AgencyEmployeeNumber;
    this.citizenModel.CitizenshipId = currentCitizen.CitizenshipId;
    this.citizenModel.TypeOfCitizenshipId = currentCitizen.TypeOfCitizenshipId;
    this.citizenModel.DualCitizenshipCountry = currentCitizen.DualCitizenshipCountry;
    this.citizenModel.ResidentialNumber = currentCitizen.ResidentialNumber;
    this.citizenModel.ResidentialStreet = currentCitizen.ResidentialStreet;
    this.citizenModel.ResidentialVillage = currentCitizen.ResidentialVillage;
    this.citizenModel.ResidentialBarangayId = currentCitizen.ResidentialBarangayId;
    this.citizenModel.ResidentialCityId = currentCitizen.ResidentialCityId;
    this.citizenModel.ResidentialProvinceId = currentCitizen.ResidentialProvinceId;
    this.citizenModel.ResidentialZipCode = currentCitizen.ResidentialZipCode;
    this.citizenModel.PermanentNumber = currentCitizen.PermanentNumber;
    this.citizenModel.PermanentStreet = currentCitizen.PermanentStreet;
    this.citizenModel.PermanentVillage = currentCitizen.PermanentVillage;
    this.citizenModel.PermanentBarangayId = currentCitizen.PermanentBarangayId;
    this.citizenModel.PermanentCityId = currentCitizen.PermanentCityId;
    this.citizenModel.PermanentProvinceId = currentCitizen.PermanentProvinceId;
    this.citizenModel.PermanentZipCode = currentCitizen.PermanentZipCode;
    this.citizenModel.TelephoneNumber = currentCitizen.TelephoneNumber;
    this.citizenModel.MobileNumber = currentCitizen.MobileNumber;
    this.citizenModel.EmailAddress = currentCitizen.EmailAddress;
    this.citizenModel.OccupationId = currentCitizen.OccupationId;
    this.citizenModel.EmployerName = currentCitizen.EmployerName;
    this.citizenModel.EmployerAddress = currentCitizen.EmployerAddress;
    this.citizenModel.EmployerTelephoneNumber = currentCitizen.EmployerTelephoneNumber;
    this.citizenModel.SpouseSurname = currentCitizen.SpouseSurname;
    this.citizenModel.SpouseFirstname = currentCitizen.SpouseFirstname;
    this.citizenModel.SpouseMiddlename = currentCitizen.SpouseMiddlename;
    this.citizenModel.SpouseExtensionname = currentCitizen.SpouseExtensionname;
    this.citizenModel.SpouseOccupationId = currentCitizen.SpouseOccupationId;
    this.citizenModel.SpouseEmployerName = currentCitizen.SpouseEmployerName;
    this.citizenModel.SpouseEmployerAddress = currentCitizen.SpouseEmployerAddress;
    this.citizenModel.FatherSurname = currentCitizen.FatherSurname;
    this.citizenModel.FatherFirstname = currentCitizen.FatherFirstname;
    this.citizenModel.FatherMiddlename = currentCitizen.FatherMiddlename;
    this.citizenModel.FatherExtensionname = currentCitizen.FatherExtensionname;
    this.citizenModel.MotherSurname = currentCitizen.MotherSurname;
    this.citizenModel.MotherFirstname = currentCitizen.MotherFirstname;
    this.citizenModel.MotherMiddlename = currentCitizen.MotherMiddlename;
    this.citizenModel.MotherExtensionname = currentCitizen.MotherExtensionname;
    this.citizenModel.PictureURL = currentCitizen.PictureURL;
    this.citizenModel.StatusId = currentCitizen.StatusId;
    this.citizenModel.IsLocked = currentCitizen.IsLocked;

    const citizenEditDialogRef = this.citizenEditDialog.open(CitizenDetailDialogComponent, {
      width: '1200px',
      data: {
        objDialogTitle: "Edit Citizen",
        objCitizenModel: this.citizenModel
      },
      disableClose: true
    });

    citizenEditDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listCitizenData();
      }
    });
  }

  public btnDeleteCitizen(): void {
    let currentCitizen = this.listCitizenCollectionView.currentItem;

    if (currentCitizen.IsLocked == true) {
      this.toastr.error("Citizen cannot be deleted if locked.", "Error");
    } else {
      this.citizenModel.Id = currentCitizen.Id;

      const citizenDeleteDialogRef = this.citizenDeleteDialog.open(CitizenDeleteDialogComponent, {
        width: '300px',
        data: { objCitizenModel: this.citizenModel },
        disableClose: true
      });

      citizenDeleteDialogRef.afterClosed().subscribe(result => {
        if (result == 200) {
          this.listCitizenData();
        }
      });
    }
  }

  public btnSearchCitizen(): void {
    const citizenSearchDialogRef = this.citizenSearchDialog.open(CitizenSearchDialogComponent, {
      width: '800px',
      disableClose: true
    });

    citizenSearchDialogRef.afterClosed().subscribe(result => {
      if (result != null) {
        if (result[1] != "" || result[1] != null) {
          this.isFilterShow = true;
          this.filterString = result[1];
        }

        this.listCitizenObservableArray = result[0];
        this.listCitizenCollectionView = new CollectionView(this.listCitizenObservableArray);
        this.listCitizenCollectionView.pageSize = this.listCitizenPageIndex;
        this.listCitizenCollectionView.trackChanges = true;
        this.listCitizenCollectionView.refresh();
      }
    });
  }

  public btnClearFilter(): void {
    this.isFilterShow = false;
    this.filterString = "";

    this.listCitizenData();
  }

  ngOnInit() {
    this.listCitizenData();
  }

  ngOnDestroy() {
    if (this.listCitizenSubscription != null) this.listCitizenSubscription.unsubscribe();
    if (this.addCitizenSubscription != null) this.addCitizenSubscription.unsubscribe();
    if (this.detailCitizenSubscription != null) this.detailCitizenSubscription.unsubscribe();
  }
}
