import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CitizenService } from '../citizen.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-citizen-search-dialog',
  templateUrl: './citizen-search-dialog.component.html',
  styleUrls: ['./citizen-search-dialog.component.css']
})
export class CitizenSearchDialogComponent implements OnInit {

  constructor(
    private citizenService: CitizenService,
    public citizenSearchDialogRef: MatDialogRef<CitizenSearchDialogComponent>,
    private toastr: ToastrService
  ) { }

  public searchKeyword: string = "";
  public searchCitizenSubscription: any;

  public btnSearchCitizen(): void {
    if (this.searchKeyword == "" || this.searchKeyword == null) {
      this.toastr.error("Search input cannot be empty.", "Invalid Search");
    } else {
      let btnSearchCitizen: Element = document.getElementById("btnSearchCitizen");
      let btnCloseSearchCitizen: Element = document.getElementById("btnCloseSearchCitizen");

      btnSearchCitizen.setAttribute("disabled", "disabled");
      btnCloseSearchCitizen.setAttribute("disabled", "disabled");

      this.citizenService.searchCitizen(this.searchKeyword);
      this.searchCitizenSubscription = this.citizenService.searchCitizenObservable.subscribe(
        data => {
          this.toastr.success("Search successfully done.", "Success");

          this.citizenSearchDialogRef.close([data, this.searchKeyword]);

          btnSearchCitizen.removeAttribute("disabled");
          btnCloseSearchCitizen.removeAttribute("disabled");

          if (this.searchCitizenSubscription != null) this.searchCitizenSubscription.unsubscribe();
        }
      );
    }
  }

  public btnCloseSearchDialog(): void {
    this.citizenSearchDialogRef.close();

    if (this.searchCitizenSubscription != null) this.searchCitizenSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.searchCitizenSubscription != null) this.searchCitizenSubscription.unsubscribe();
  }

}
