import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { OccupationModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-occupation-detail-dialog',
  templateUrl: './occupation-detail-dialog.component.html',
  styleUrls: ['./occupation-detail-dialog.component.css']
})
export class OccupationDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public occupationDetailDialogRef: MatDialogRef<OccupationDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public occupationData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Occupation Detail";

  public occupationModel: OccupationModel = {
    Id: this.occupationData.objOccupationModel.Id,
    Occupation: this.occupationData.objOccupationModel.Occupation
  }

  public saveOccupationSubscription: any;

  public btnSaveOccupation(): void {
    let btnSaveOccupation: Element = document.getElementById("btnSaveOccupation");
    let btnCloseOccupationDialog: Element = document.getElementById("btnCloseOccupationDialog");

    btnSaveOccupation.setAttribute("disabled", "disabled");
    btnCloseOccupationDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveOccupation(this.occupationModel);
    this.saveOccupationSubscription = this.systemTablesService.saveOccupationObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Occupation was successfully saved.", "Success");
          this.occupationDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveOccupation.removeAttribute("disabled");
          btnCloseOccupationDialog.removeAttribute("disabled");
        }

        if (this.saveOccupationSubscription != null) this.saveOccupationSubscription.unsubscribe();
      }
    );
  }

  public btnCloseOccupationDialog(): void {
    this.occupationDetailDialogRef.close();

    if (this.saveOccupationSubscription != null) this.saveOccupationSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.occupationData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveOccupationSubscription != null) this.saveOccupationSubscription.unsubscribe();
  }

}
