import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-sex-delete-dialog',
  templateUrl: './sex-delete-dialog.component.html',
  styleUrls: ['./sex-delete-dialog.component.css']
})
export class SexDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public sexDeleteDialogRef: MatDialogRef<SexDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public sexData: any,
    private toastr: ToastrService
  ) { }

  public deleteSexSubscription: any;

  public btnDeleteSex() {
    let btnDeleteSex: Element = document.getElementById("btnDeleteSex");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteSex.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteSex(this.sexData.objSexModel.Id);
    this.deleteSexSubscription = this.systemTablesService.deleteSexObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Gender was successfully deleted.", "Success");
          this.sexDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteSex.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteSexSubscription != null) this.deleteSexSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.sexDeleteDialogRef.close();

    if (this.deleteSexSubscription != null) this.deleteSexSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteSexSubscription != null) this.deleteSexSubscription.unsubscribe();
  }

}
