import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-education-level-delete-dialog',
  templateUrl: './education-level-delete-dialog.component.html',
  styleUrls: ['./education-level-delete-dialog.component.css']
})
export class EducationLevelDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public educationLevelDeleteDialogRef: MatDialogRef<EducationLevelDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public educationLevelData: any,
    private toastr: ToastrService
  ) { }

  public deleteEducationLevelSubscription: any;

  public btnDeleteEducationLevel() {
    let btnDeleteEducationLevel: Element = document.getElementById("btnDeleteEducationLevel");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteEducationLevel.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteEducationLevel(this.educationLevelData.objEducationLevelModel.Id);
    this.deleteEducationLevelSubscription = this.systemTablesService.deleteEducationLevelObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Education level was successfully deleted.", "Success");
          this.educationLevelDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteEducationLevel.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteEducationLevelSubscription != null) this.deleteEducationLevelSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.educationLevelDeleteDialogRef.close();

    if (this.deleteEducationLevelSubscription != null) this.deleteEducationLevelSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteEducationLevelSubscription != null) this.deleteEducationLevelSubscription.unsubscribe();
  }

}
