import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CitizenModel } from '../citizen.model';

import { CitizenService } from '../citizen.service';

import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-citizen-delete-dialog',
  templateUrl: './citizen-delete-dialog.component.html',
  styleUrls: ['./citizen-delete-dialog.component.css']
})
export class CitizenDeleteDialogComponent implements OnInit {

  constructor(
    private citizenService: CitizenService,
    public citizenDeleteDialogRef: MatDialogRef<CitizenDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public citizenData: any,
    private toastr: ToastrService
  ) { }

  public deleteCitizenSubscription: any;

  public btnDeleteCitizen() {
    let btnDeleteCitizen: Element = document.getElementById("btnDeleteCitizen");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteCitizen.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.citizenService.deleteCitizen(this.citizenData.objCitizenModel.Id);
    this.deleteCitizenSubscription = this.citizenService.deleteCitizenObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Citizen was successfully deleted.", "Success");
          this.citizenDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteCitizen.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteCitizenSubscription != null) this.deleteCitizenSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.citizenDeleteDialogRef.close();
    
    if (this.deleteCitizenSubscription != null) this.deleteCitizenSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteCitizenSubscription != null) this.deleteCitizenSubscription.unsubscribe();
  }
}
