import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CitizensChildrenModel } from './citizen-children.model'
import { CitizenService } from '../../citizen.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-citizen-children-detail-dialog',
  templateUrl: './citizen-children-detail-dialog.component.html',
  styleUrls: ['./citizen-children-detail-dialog.component.css']
})
export class CitizenChildrenDetailDialogComponent implements OnInit {

  constructor(
    private citizenService: CitizenService,
    public citizenDetailChildrenDialogRef: MatDialogRef<CitizenChildrenDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public citizensChildrenData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Citizen's Child Detail";

  public citizensChildrenModel: CitizensChildrenModel = {
    Id: this.citizensChildrenData.objCitizensChildrenModel.Id,
    CitizenId: this.citizensChildrenData.objCitizensChildrenModel.CitizenId,
    Fullname: this.citizensChildrenData.objCitizensChildrenModel.Fullname,
    DateOfBirth: this.citizensChildrenData.objCitizensChildrenModel.DateOfBirth
  }

  public saveCitizensChildrenSubscription: any;

  public btnSaveCitizensChildren(): void {
    let btnSaveCitizensChildren: Element = document.getElementById("btnSaveCitizensChildren");
    let btnCloseCitizensChildrenDialog: Element = document.getElementById("btnCloseCitizensChildrenDialog");

    btnSaveCitizensChildren.setAttribute("disabled", "disabled");
    btnCloseCitizensChildrenDialog.setAttribute("disabled", "disabled");

    this.citizenService.saveCitizensChildren(this.citizensChildrenModel);
    this.saveCitizensChildrenSubscription = this.citizenService.saveCitizensChildrenObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Child was successfully saved.", "Success");
          this.citizenDetailChildrenDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveCitizensChildren.removeAttribute("disabled");
          btnCloseCitizensChildrenDialog.removeAttribute("disabled");
        }

        if (this.saveCitizensChildrenSubscription != null) this.saveCitizensChildrenSubscription.unsubscribe();
      }
    );
  }

  public btnCloseCitizensChildrenDialog(): void {
    this.citizenDetailChildrenDialogRef.close();

    if (this.saveCitizensChildrenSubscription != null) this.saveCitizensChildrenSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.citizensChildrenData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveCitizensChildrenSubscription != null) this.saveCitizensChildrenSubscription.unsubscribe();
  }

}
