  public listSex(): void {
    this.citizenService.listSex();
    this.listSexSubscription = this.citizenService.listSexObservable.subscribe(
      data => {
        this.listSexObservableArray = data;
        if (this.listSexSubscription != null) this.listSexSubscription.unsubscribe();

        this.listCivilStatus();
      }
    );
  }

