  ngOnDestroy() {
    if (this.listSexSubscription != null) this.listSexSubscription.unsubscribe();
    if (this.listCivilStatusSubscription != null) this.listCivilStatusSubscription.unsubscribe();
    if (this.listBloodTypeSubscription != null) this.listBloodTypeSubscription.unsubscribe();
    if (this.listCitizenshipSubscription != null) this.listCitizenshipSubscription.unsubscribe();
    if (this.listTypeOfCitizenshipSubscription != null) this.listTypeOfCitizenshipSubscription.unsubscribe();
    if (this.listBarangaySubscription != null) this.listBarangaySubscription.unsubscribe();
    if (this.listCitySubscription != null) this.listCitySubscription.unsubscribe();
    if (this.listProvinceSubscription != null) this.listProvinceSubscription.unsubscribe();
    if (this.listOccupationSubscription != null) this.listOccupationSubscription.unsubscribe();
    if (this.listStatusSubscription != null) this.listStatusSubscription.unsubscribe();
    if (this.saveCitizenSubscription != null) this.saveCitizenSubscription.unsubscribe();
    if (this.lockCitizenSubscription != null) this.lockCitizenSubscription.unsubscribe();
    if (this.unlockCitizenSubscription != null) this.unlockCitizenSubscription.unsubscribe();

    if (this.listCitizensChildrenSubscription != null) this.listCitizensChildrenSubscription.unsubscribe();
    if (this.listCitizensEducationSubscription != null) this.listCitizensEducationSubscription.unsubscribe();
  }
