import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CitizensEducationModel } from './citizen-education.model'
import { CitizenService } from '../../citizen.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';

@Component({
  selector: 'app-citizen-education-detail-dialog',
  templateUrl: './citizen-education-detail-dialog.component.html',
  styleUrls: ['./citizen-education-detail-dialog.component.css']
})
export class CitizenEducationDetailDialogComponent implements OnInit {

  constructor(
    private citizenService: CitizenService,
    public citizenDetailEducationDialogRef: MatDialogRef<CitizenEducationDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public citizensEducationData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Citizen's Child Detail";

  public citizensEducationModel: CitizensEducationModel = {
    Id: this.citizensEducationData.objCitizensEducationModel.Id,
    CitizenId: this.citizensEducationData.objCitizensEducationModel.CitizenId,
    EducationLevelId: this.citizensEducationData.objCitizensEducationModel.EducationLevelId,
    NameOfSchool: this.citizensEducationData.objCitizensEducationModel.NameOfSchool,
    Degree: this.citizensEducationData.objCitizensEducationModel.Degree,
    PeriodFrom: this.citizensEducationData.objCitizensEducationModel.PeriodFrom,
    PeriodTo: this.citizensEducationData.objCitizensEducationModel.PeriodTo,
    UnitsEarned: this.citizensEducationData.objCitizensEducationModel.UnitsEarned,
    YearGraduated: this.citizensEducationData.objCitizensEducationModel.YearGraduated,
  }

  public listEducationLevelSubscription: any;
  public listEducationLevelObservableArray: ObservableArray = new ObservableArray();
  public saveCitizensEducationSubscription: any;

  public listEducationLevel(): void {
    this.citizenService.listEducationLevel();
    this.listEducationLevelSubscription = this.citizenService.listEducationLevelObservable.subscribe(
      data => {
        this.listEducationLevelObservableArray = data;
        if (this.listEducationLevelSubscription != null) this.listEducationLevelSubscription.unsubscribe();
      }
    );
  }

  public btnSaveCitizensEducation(): void {
    let btnSaveCitizensEducation: Element = document.getElementById("btnSaveCitizensEducation");
    let btnCloseCitizensEducationDialog: Element = document.getElementById("btnCloseCitizensEducationDialog");

    btnSaveCitizensEducation.setAttribute("disabled", "disabled");
    btnCloseCitizensEducationDialog.setAttribute("disabled", "disabled");

    this.citizenService.saveCitizensEducation(this.citizensEducationModel);
    this.saveCitizensEducationSubscription = this.citizenService.saveCitizensEducationObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Child was successfully saved.", "Success");
          this.citizenDetailEducationDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveCitizensEducation.removeAttribute("disabled");
          btnCloseCitizensEducationDialog.removeAttribute("disabled");
        }

        if (this.saveCitizensEducationSubscription != null) this.saveCitizensEducationSubscription.unsubscribe();
      }
    );
  }

  public btnCloseCitizensEducationDialog(): void {
    this.citizenDetailEducationDialogRef.close();

    if (this.listEducationLevelSubscription != null) this.listEducationLevelSubscription.unsubscribe();
    if (this.saveCitizensEducationSubscription != null) this.saveCitizensEducationSubscription.unsubscribe();
  }

  ngOnInit() {
    this.listEducationLevel();
    this.dialogTitle = this.citizensEducationData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.listEducationLevelSubscription != null) this.listEducationLevelSubscription.unsubscribe();
    if (this.saveCitizensEducationSubscription != null) this.saveCitizensEducationSubscription.unsubscribe();
  }
}
