import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ServiceGroupModel } from '../services.model'
import { ServicesService } from '../services.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

@Component({
  selector: 'app-service-group-detail-dialog',
  templateUrl: './service-group-detail-dialog.component.html',
  styleUrls: ['./service-group-detail-dialog.component.css']
})
export class ServiceGroupDetailDialogComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public serviceGroupDetailDialogRef: MatDialogRef<ServiceGroupDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceGroupData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Service Group Detail";

  public serviceGroupModel: ServiceGroupModel = {
    Id: this.serviceGroupData.objServiceGroupModel.Id,
    ServiceGroup: this.serviceGroupData.objServiceGroupModel.ServiceGroup,
    Description: this.serviceGroupData.objServiceGroupModel.Description,
    ServiceDepartmentId: this.serviceGroupData.objServiceGroupModel.ServiceDepartmentId
  }

  public listDropdownServiceDepartmentSubscription: any;
  public listDropdownServiceDepartmentObservableArray: ObservableArray = new ObservableArray();

  public saveServiceGroupSubscription: any;

  public listDropdownServiceDepartment(): void {
    this.servicesService.listDropdownServiceDepartment();
    this.listDropdownServiceDepartmentSubscription = this.servicesService.listDropdownServiceDepartmentObservable.subscribe(
      data => {
        this.listDropdownServiceDepartmentObservableArray = data;
        if (this.listDropdownServiceDepartmentSubscription != null) this.listDropdownServiceDepartmentSubscription.unsubscribe();
      }
    );
  }

  public btnSaveServiceGroup(): void {
    let btnSaveServiceGroup: Element = document.getElementById("btnSaveServiceGroup");
    let btnCloseServiceGroupDialog: Element = document.getElementById("btnCloseServiceGroupDialog");

    btnSaveServiceGroup.setAttribute("disabled", "disabled");
    btnCloseServiceGroupDialog.setAttribute("disabled", "disabled");

    this.servicesService.saveServiceGroup(this.serviceGroupModel);
    this.saveServiceGroupSubscription = this.servicesService.saveServiceGroupObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Service group was successfully saved.", "Success");
          this.serviceGroupDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveServiceGroup.removeAttribute("disabled");
          btnCloseServiceGroupDialog.removeAttribute("disabled");
        }

        if (this.saveServiceGroupSubscription != null) this.saveServiceGroupSubscription.unsubscribe();
      }
    );
  }

  public btnCloseServiceGroupDialog(): void {
    this.serviceGroupDetailDialogRef.close();

    if (this.listDropdownServiceDepartmentSubscription != null) this.listDropdownServiceDepartmentSubscription.unsubscribe();
    if (this.saveServiceGroupSubscription != null) this.saveServiceGroupSubscription.unsubscribe();
  }

  ngOnInit() {
    this.listDropdownServiceDepartment();
    this.dialogTitle = this.serviceGroupData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.listDropdownServiceDepartmentSubscription != null) this.listDropdownServiceDepartmentSubscription.unsubscribe();
    if (this.saveServiceGroupSubscription != null) this.saveServiceGroupSubscription.unsubscribe();
  }

}
