import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProvinceModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

@Component({
  selector: 'app-province-detail-dialog',
  templateUrl: './province-detail-dialog.component.html',
  styleUrls: ['./province-detail-dialog.component.css']
})
export class ProvinceDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public provinceDetailDialogRef: MatDialogRef<ProvinceDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public provinceData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Province Detail";

  public provinceModel: ProvinceModel = {
    Id: this.provinceData.objProvinceModel.Id,
    Province: this.provinceData.objProvinceModel.Province,
    CountryId: this.provinceData.objProvinceModel.CountryId,
    RegionId: this.provinceData.objProvinceModel.RegionId
  }

  public listProvinceDropdownCountrySubscription: any;
  public listProvinceDropdownCountryObservableArray: ObservableArray = new ObservableArray();
  public listProvinceDropdownRegionSubscription: any;
  public listProvinceDropdownRegionObservableArray: ObservableArray = new ObservableArray();

  public saveProvinceSubscription: any;

  public listDropdownCountry(): void {
    this.systemTablesService.listProvinceDropdownCountry();
    this.listProvinceDropdownCountrySubscription = this.systemTablesService.listProvinceDropdownCountryObservable.subscribe(
      data => {
        this.listProvinceDropdownCountryObservableArray = data;
        this.listDropdownRegion(this.provinceModel.CountryId);

        if (this.listProvinceDropdownCountrySubscription != null) this.listProvinceDropdownCountrySubscription.unsubscribe();
      }
    );
  }

  public listDropdownRegion(countryId: number): void {
    this.systemTablesService.listProvinceDropdownRegion(countryId);
    this.listProvinceDropdownRegionSubscription = this.systemTablesService.listProvinceDropdownRegionObservable.subscribe(
      data => {
        this.listProvinceDropdownRegionObservableArray = data;

        if (data.length <= 0) {
          this.provinceModel.RegionId = 0;
        }

        if (this.listProvinceDropdownRegionSubscription != null) this.listProvinceDropdownRegionSubscription.unsubscribe();
      }
    );
  }

  public btnSaveProvince(): void {
    let btnSaveProvince: Element = document.getElementById("btnSaveProvince");
    let btnCloseProvinceDialog: Element = document.getElementById("btnCloseProvinceDialog");

    btnSaveProvince.setAttribute("disabled", "disabled");
    btnCloseProvinceDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveProvince(this.provinceModel);
    this.saveProvinceSubscription = this.systemTablesService.saveProvinceObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Province was successfully saved.", "Success");
          this.provinceDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveProvince.removeAttribute("disabled");
          btnCloseProvinceDialog.removeAttribute("disabled");
        }

        if (this.saveProvinceSubscription != null) this.saveProvinceSubscription.unsubscribe();
      }
    );
  }

  public btnCloseProvinceDialog(): void {
    this.provinceDetailDialogRef.close();

    if (this.listProvinceDropdownCountrySubscription != null) this.listProvinceDropdownCountrySubscription.unsubscribe();
    if (this.listProvinceDropdownRegionSubscription != null) this.listProvinceDropdownRegionSubscription.unsubscribe();
    if (this.saveProvinceSubscription != null) this.saveProvinceSubscription.unsubscribe();
  }

  public provinceModelChange(): void {
    this.listDropdownRegion(this.provinceModel.CountryId);
  }

  ngOnInit() {
    this.dialogTitle = this.provinceData.objDialogTitle;
    this.listDropdownCountry();
  }

  ngOnDestroy() {
    if (this.listProvinceDropdownCountrySubscription != null) this.listProvinceDropdownCountrySubscription.unsubscribe();
    if (this.listProvinceDropdownRegionSubscription != null) this.listProvinceDropdownRegionSubscription.unsubscribe();
    if (this.saveProvinceSubscription != null) this.saveProvinceSubscription.unsubscribe();
  }

}
