import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { StatusModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-status-detail-dialog',
  templateUrl: './status-detail-dialog.component.html',
  styleUrls: ['./status-detail-dialog.component.css']
})
export class StatusDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public statusDetailDialogRef: MatDialogRef<StatusDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public statusData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Status Detail";

  public statusModel: StatusModel = {
    Id: this.statusData.objStatusModel.Id,
    Status: this.statusData.objStatusModel.Status,
    Category: this.statusData.objStatusModel.Category
  }

  public saveStatusSubscription: any;

  public btnSaveStatus(): void {
    let btnSaveStatus: Element = document.getElementById("btnSaveStatus");
    let btnCloseStatusDialog: Element = document.getElementById("btnCloseStatusDialog");

    btnSaveStatus.setAttribute("disabled", "disabled");
    btnCloseStatusDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveStatus(this.statusModel);
    this.saveStatusSubscription = this.systemTablesService.saveStatusObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Status was successfully saved.", "Success");
          this.statusDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveStatus.removeAttribute("disabled");
          btnCloseStatusDialog.removeAttribute("disabled");
        }

        if (this.saveStatusSubscription != null) this.saveStatusSubscription.unsubscribe();
      }
    );
  }

  public btnCloseStatusDialog(): void {
    this.statusDetailDialogRef.close();

    if (this.saveStatusSubscription != null) this.saveStatusSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.statusData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveStatusSubscription != null) this.saveStatusSubscription.unsubscribe();
  }

}
