import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CitizensEducationModel } from '../citizen-education-detail-dialog/citizen-education.model';
import { CitizenService } from '../../citizen.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-citizen-education-delete-dialog',
  templateUrl: './citizen-education-delete-dialog.component.html',
  styleUrls: ['./citizen-education-delete-dialog.component.css']
})
export class CitizenEducationDeleteDialogComponent implements OnInit {

  constructor(
    private citizenService: CitizenService,
    public citizenEducationDeleteDialogRef: MatDialogRef<CitizenEducationDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public childData: any,
    private toastr: ToastrService
  ) { }

  public deleteCitizensEducationSubscription: any;

  public btnDeleteEducation() {
    let btnDeleteEducation: Element = document.getElementById("btnDeleteEducation");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteEducation.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.citizenService.deleteCitizensEducation(this.childData.objCitizensEducationModel.Id);
    this.deleteCitizensEducationSubscription = this.citizenService.deleteCitizensEducationObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Education was successfully deleted.", "Success");
          this.citizenEducationDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteEducation.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteCitizensEducationSubscription != null) this.deleteCitizensEducationSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.citizenEducationDeleteDialogRef.close();

    if (this.deleteCitizensEducationSubscription != null) this.deleteCitizensEducationSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteCitizensEducationSubscription != null) this.deleteCitizensEducationSubscription.unsubscribe();
  }
}
