  public listCitizenship(): void {
    let listCitizenshipObservableArray = new ObservableArray();
    this.listCitizenshipSubject.next(listCitizenshipObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/mst/citizenship/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listCitizenshipObservableArray.push({
              Id: results[i].Id,
              Citizenship: results[i].Citizenship
            });
          }
        }

        this.listCitizenshipSubject.next(listCitizenshipObservableArray);
      }
    );
  }

  public saveCitizenship(objCitizensChildModel: CitizenshipModel): void {
    if (objCitizensChildModel.Id == 0) {
      this.httpClient.post(this.defaultAPIURLHost + "/api/mst/citizenship/add", JSON.stringify(objCitizensChildModel), this.options).subscribe(
        response => {
          let responseResults: string[] = ["success", response.toString()];
          this.saveCitizenshipSubject.next(responseResults);
        },
        error => {
          let errorResults: string[] = ["failed", error["error"]];
          this.saveCitizenshipSubject.next(errorResults);
        }
      );
    } else {
      this.httpClient.put(this.defaultAPIURLHost + "/api/mst/citizenship/update/" + objCitizensChildModel.Id, JSON.stringify(objCitizensChildModel), this.options).subscribe(
        response => {
          let responseResults: string[] = ["success", response.toString()];
          this.saveCitizenshipSubject.next(responseResults);
        },
        error => {
          let errorResults: string[] = ["failed", error["error"]];
          this.saveCitizenshipSubject.next(errorResults);
        }
      );
    }
  }

  public deleteCitizenship(id: number): void {
    this.httpClient.delete(this.defaultAPIURLHost + "/api/mst/citizenship/delete/" + id, this.options).subscribe(
      response => {
        let responseResults: string[] = ["success", ""];
        this.deleteCitizenshipSubject.next(responseResults);
      },
      error => {
        let errorResults: string[] = ["failed", error["error"]];
        this.deleteCitizenshipSubject.next(errorResults);
      }
    );
  }

