import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ServicesService } from '../services.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-service-group-delete-dialog',
  templateUrl: './service-group-delete-dialog.component.html',
  styleUrls: ['./service-group-delete-dialog.component.css']
})
export class ServiceGroupDeleteDialogComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public serviceGroupDeleteDialogRef: MatDialogRef<ServiceGroupDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceGroupData: any,
    private toastr: ToastrService
  ) { }

  public deleteServiceGroupSubscription: any;

  public btnDeleteServiceGroup() {
    let btnDeleteServiceGroup: Element = document.getElementById("btnDeleteServiceGroup");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteServiceGroup.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.servicesService.deleteServiceGroup(this.serviceGroupData.objServiceGroupModel.Id);
    this.deleteServiceGroupSubscription = this.servicesService.deleteServiceGroupObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Service group was successfully deleted.", "Success");
          this.serviceGroupDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteServiceGroup.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteServiceGroupSubscription != null) this.deleteServiceGroupSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.serviceGroupDeleteDialogRef.close();

    if (this.deleteServiceGroupSubscription != null) this.deleteServiceGroupSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteServiceGroupSubscription != null) this.deleteServiceGroupSubscription.unsubscribe();
  }

}
