import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SexModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-sex-detail-dialog',
  templateUrl: './sex-detail-dialog.component.html',
  styleUrls: ['./sex-detail-dialog.component.css']
})
export class SexDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public sexDetailDialogRef: MatDialogRef<SexDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public sexData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Gender Detail";

  public sexModel: SexModel = {
    Id: this.sexData.objSexModel.Id,
    Sex: this.sexData.objSexModel.Sex
  }

  public saveSexSubscription: any;

  public btnSaveSex(): void {
    let btnSaveSex: Element = document.getElementById("btnSaveSex");
    let btnCloseSexDialog: Element = document.getElementById("btnCloseSexDialog");

    btnSaveSex.setAttribute("disabled", "disabled");
    btnCloseSexDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveSex(this.sexModel);
    this.saveSexSubscription = this.systemTablesService.saveSexObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Gender was successfully saved.", "Success");
          this.sexDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveSex.removeAttribute("disabled");
          btnCloseSexDialog.removeAttribute("disabled");
        }

        if (this.saveSexSubscription != null) this.saveSexSubscription.unsubscribe();
      }
    );
  }

  public btnCloseSexDialog(): void {
    this.sexDetailDialogRef.close();

    if (this.saveSexSubscription != null) this.saveSexSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.sexData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveSexSubscription != null) this.saveSexSubscription.unsubscribe();
  }

}
