import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { RegionModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

@Component({
  selector: 'app-region-detail-dialog',
  templateUrl: './region-detail-dialog.component.html',
  styleUrls: ['./region-detail-dialog.component.css']
})
export class RegionDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public regionDetailDialogRef: MatDialogRef<RegionDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public regionData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Region Detail";

  public regionModel: RegionModel = {
    Id: this.regionData.objRegionModel.Id,
    Region: this.regionData.objRegionModel.Region,
    CountryId: this.regionData.objRegionModel.CountryId
  }

  public listRegionDropdownCountrySubscription: any;
  public listRegionDropdownCountryObservableArray: ObservableArray = new ObservableArray();

  public saveRegionSubscription: any;

  public listDropdownCountry(): void {
    this.systemTablesService.listRegionDropdownCountry();
    this.listRegionDropdownCountrySubscription = this.systemTablesService.listRegionDropdownCountryObservable.subscribe(
      data => {
        this.listRegionDropdownCountryObservableArray = data;
        if (this.listRegionDropdownCountrySubscription != null) this.listRegionDropdownCountrySubscription.unsubscribe();
      }
    );
  }

  public btnSaveRegion(): void {
    let btnSaveRegion: Element = document.getElementById("btnSaveRegion");
    let btnCloseRegionDialog: Element = document.getElementById("btnCloseRegionDialog");

    btnSaveRegion.setAttribute("disabled", "disabled");
    btnCloseRegionDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveRegion(this.regionModel);
    this.saveRegionSubscription = this.systemTablesService.saveRegionObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Region was successfully saved.", "Success");
          this.regionDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveRegion.removeAttribute("disabled");
          btnCloseRegionDialog.removeAttribute("disabled");
        }

        if (this.saveRegionSubscription != null) this.saveRegionSubscription.unsubscribe();
      }
    );
  }

  public btnCloseRegionDialog(): void {
    this.regionDetailDialogRef.close();

    if (this.listRegionDropdownCountrySubscription != null) this.listRegionDropdownCountrySubscription.unsubscribe();
    if (this.saveRegionSubscription != null) this.saveRegionSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.regionData.objDialogTitle;
    this.listDropdownCountry();
  }

  ngOnDestroy() {
    if (this.listRegionDropdownCountrySubscription != null) this.listRegionDropdownCountrySubscription.unsubscribe();
    if (this.saveRegionSubscription != null) this.saveRegionSubscription.unsubscribe();
  }

}
