import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CountryModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-country-detail-dialog',
  templateUrl: './country-detail-dialog.component.html',
  styleUrls: ['./country-detail-dialog.component.css']
})
export class CountryDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public countryDetailDialogRef: MatDialogRef<CountryDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public countryData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Country Detail";

  public countryModel: CountryModel = {
    Id: this.countryData.objCountryModel.Id,
    Country: this.countryData.objCountryModel.Country
  }

  public saveCountrySubscription: any;

  public btnSaveCountry(): void {
    let btnSaveCountry: Element = document.getElementById("btnSaveCountry");
    let btnCloseCountryDialog: Element = document.getElementById("btnCloseCountryDialog");

    btnSaveCountry.setAttribute("disabled", "disabled");
    btnCloseCountryDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveCountry(this.countryModel);
    this.saveCountrySubscription = this.systemTablesService.saveCountryObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Country was successfully saved.", "Success");
          this.countryDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveCountry.removeAttribute("disabled");
          btnCloseCountryDialog.removeAttribute("disabled");
        }

        if (this.saveCountrySubscription != null) this.saveCountrySubscription.unsubscribe();
      }
    );
  }

  public btnCloseCountryDialog(): void {
    this.countryDetailDialogRef.close();

    if (this.saveCountrySubscription != null) this.saveCountrySubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.countryData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveCountrySubscription != null) this.saveCountrySubscription.unsubscribe();
  }

}
