import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CitizenshipModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-citizenship-detail-dialog',
  templateUrl: './citizenship-detail-dialog.component.html',
  styleUrls: ['./citizenship-detail-dialog.component.css']
})
export class CitizenshipDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public citizenshipDetailDialogRef: MatDialogRef<CitizenshipDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public citizenshipData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Citizenship Detail";

  public citizenshipModel: CitizenshipModel = {
    Id: this.citizenshipData.objCitizenshipModel.Id,
    Citizenship: this.citizenshipData.objCitizenshipModel.Citizenship
  }

  public saveCitizenshipSubscription: any;

  public btnSaveCitizenship(): void {
    let btnSaveCitizenship: Element = document.getElementById("btnSaveCitizenship");
    let btnCloseCitizenshipDialog: Element = document.getElementById("btnCloseCitizenshipDialog");

    btnSaveCitizenship.setAttribute("disabled", "disabled");
    btnCloseCitizenshipDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveCitizenship(this.citizenshipModel);
    this.saveCitizenshipSubscription = this.systemTablesService.saveCitizenshipObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Citizenship was successfully saved.", "Success");
          this.citizenshipDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveCitizenship.removeAttribute("disabled");
          btnCloseCitizenshipDialog.removeAttribute("disabled");
        }

        if (this.saveCitizenshipSubscription != null) this.saveCitizenshipSubscription.unsubscribe();
      }
    );
  }

  public btnCloseCitizenshipDialog(): void {
    this.citizenshipDetailDialogRef.close();

    if (this.saveCitizenshipSubscription != null) this.saveCitizenshipSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.citizenshipData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveCitizenshipSubscription != null) this.saveCitizenshipSubscription.unsubscribe();
  }

}
