import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CardService } from '../../card.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-card-search-citizen-dialog',
  templateUrl: './card-search-citizen-dialog.component.html',
  styleUrls: ['./card-search-citizen-dialog.component.css']
})
export class CardSearchCitizenDialogComponent implements OnInit {

  constructor(
    private cardService: CardService,
    public citizenSearchDialogRef: MatDialogRef<CardSearchCitizenDialogComponent>,
    private toastr: ToastrService
  ) { }

  public searchKeyword: string = "";
  public sarchCitizenSubscription: any;

  public btnSarchCitizen(): void {
    if (this.searchKeyword == "" || this.searchKeyword == null) {
      this.toastr.error("Search input cannot be empty.", "Invalid Search");
    } else {
      let btnSarchCitizen: Element = document.getElementById("btnSarchCitizen");
      let btnCloseSarchCitizen: Element = document.getElementById("btnCloseSarchCitizen");

      btnSarchCitizen.setAttribute("disabled", "disabled");
      btnCloseSarchCitizen.setAttribute("disabled", "disabled");

      this.cardService.searchCitizen(this.searchKeyword);
      this.sarchCitizenSubscription = this.cardService.searchCitizenListObservable.subscribe(
        data => {
          this.toastr.success("Search successfully done.", "Success");

          this.citizenSearchDialogRef.close([data, this.searchKeyword]);

          btnSarchCitizen.removeAttribute("disabled");
          btnCloseSarchCitizen.removeAttribute("disabled");

          if (this.sarchCitizenSubscription != null) this.sarchCitizenSubscription.unsubscribe();
        }
      );
    }
  }

  public btnCloseSearchDialog(): void {
    this.citizenSearchDialogRef.close();

    if (this.sarchCitizenSubscription != null) this.sarchCitizenSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.sarchCitizenSubscription != null) this.sarchCitizenSubscription.unsubscribe();
  }

}
