import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-citizenship-delete-dialog',
  templateUrl: './citizenship-delete-dialog.component.html',
  styleUrls: ['./citizenship-delete-dialog.component.css']
})
export class CitizenshipDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public citizenshipDeleteDialogRef: MatDialogRef<CitizenshipDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public citizenshipData: any,
    private toastr: ToastrService
  ) { }

  public deleteCitizenshipSubscription: any;

  public btnDeleteCitizenship() {
    let btnDeleteCitizenship: Element = document.getElementById("btnDeleteCitizenship");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteCitizenship.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteCitizenship(this.citizenshipData.objCitizenshipModel.Id);
    this.deleteCitizenshipSubscription = this.systemTablesService.deleteCitizenshipObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Citizenship was successfully deleted.", "Success");
          this.citizenshipDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteCitizenship.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteCitizenshipSubscription != null) this.deleteCitizenshipSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.citizenshipDeleteDialogRef.close();

    if (this.deleteCitizenshipSubscription != null) this.deleteCitizenshipSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteCitizenshipSubscription != null) this.deleteCitizenshipSubscription.unsubscribe();
  }

}
