import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CivilStatusModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-civil-status-detail-dialog',
  templateUrl: './civil-status-detail-dialog.component.html',
  styleUrls: ['./civil-status-detail-dialog.component.css']
})
export class CivilStatusDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public civilStatusDetailDialogRef: MatDialogRef<CivilStatusDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public civilStatusData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Civil Status Detail";

  public civilStatusModel: CivilStatusModel = {
    Id: this.civilStatusData.objCivilStatusModel.Id,
    CivilStatus: this.civilStatusData.objCivilStatusModel.CivilStatus
  }

  public saveCivilStatusSubscription: any;

  public btnSaveCivilStatus(): void {
    let btnSaveCivilStatus: Element = document.getElementById("btnSaveCivilStatus");
    let btnCloseCivilStatusDialog: Element = document.getElementById("btnCloseCivilStatusDialog");

    btnSaveCivilStatus.setAttribute("disabled", "disabled");
    btnCloseCivilStatusDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveCivilStatus(this.civilStatusModel);
    this.saveCivilStatusSubscription = this.systemTablesService.saveCivilStatusObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Civil status was successfully saved.", "Success");
          this.civilStatusDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveCivilStatus.removeAttribute("disabled");
          btnCloseCivilStatusDialog.removeAttribute("disabled");
        }

        if (this.saveCivilStatusSubscription != null) this.saveCivilStatusSubscription.unsubscribe();
      }
    );
  }

  public btnCloseCivilStatusDialog(): void {
    this.civilStatusDetailDialogRef.close();

    if (this.saveCivilStatusSubscription != null) this.saveCivilStatusSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.civilStatusData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveCivilStatusSubscription != null) this.saveCivilStatusSubscription.unsubscribe();
  }

}
