import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Subject, Observable } from 'rxjs';
import { WebcamImage, WebcamInitError, WebcamUtil } from 'ngx-webcam';

@Component({
  selector: 'app-citizen-camera-dialog',
  templateUrl: './citizen-camera-dialog.component.html',
  styleUrls: ['./citizen-camera-dialog.component.css']
})
export class CitizenCameraDialogComponent implements OnInit {
  constructor(
    public cameraDialogRef: MatDialogRef<CitizenCameraDialogComponent>,
  ) { }

  @Output()
  public pictureTaken = new EventEmitter<WebcamImage>();
  private trigger: Subject<void> = new Subject<void>();

  public btnCapturePhoto(): void {
    this.trigger.next();
  }

  public handleImage(webcamImage: WebcamImage): void {
    this.pictureTaken.emit(webcamImage);
    this.cameraDialogRef.close(webcamImage);
  }

  public get triggerObservable(): Observable<void> {
    return this.trigger.asObservable();
  }

  public btnCloseCameraDialog(): void {
    this.cameraDialogRef.close(null);
  }

  ngOnInit() {

  }
}
