import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SoftwareComponent } from './software.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CitizenComponent } from './citizen/citizen.component';
import { UserComponent } from './user/user.component';
import { SystemTablesComponent } from './system-tables/system-tables.component';
import { CardComponent } from './card/card.component';
import { ServicesComponent } from './services/services.component';
import { ReportsComponent } from './reports/reports.component';
import { AyudaComponent } from './ayuda/ayuda.component';

import { SoftwareRouterActivate } from './software.router.activate';

const routes: Routes = [
  {
    path: '',
    canActivate: [
      SoftwareRouterActivate
    ],
    component: SoftwareComponent,
    children: [
      {
        path: '',
        canActivate: [
          SoftwareRouterActivate
        ],
        component: DashboardComponent
      },
      {
        path: 'dashboard',
        canActivate: [
          SoftwareRouterActivate
        ],
        component: DashboardComponent
      },
      {
        path: 'citizen',
        canActivate: [
          SoftwareRouterActivate
        ],
        component: CitizenComponent
      },
      {
        path: 'user',
        canActivate: [
          SoftwareRouterActivate
        ],
        component: UserComponent
      },
      {
        path: 'system-tables',
        canActivate: [
          SoftwareRouterActivate
        ],
        component: SystemTablesComponent
      },
      {
        path: 'card',
        canActivate: [
          SoftwareRouterActivate
        ],
        component: CardComponent
      },
      {
        path: 'services',
        canActivate: [
          SoftwareRouterActivate
        ],
        component: ServicesComponent
      },
      {
        path: 'reports',
        canActivate: [
          SoftwareRouterActivate
        ],
        component: ReportsComponent
      },
      {
        path: 'ayuda',
        canActivate: [
          SoftwareRouterActivate
        ],
        component: AyudaComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SoftwareRoutingModule { }
