import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-status-delete-dialog',
  templateUrl: './status-delete-dialog.component.html',
  styleUrls: ['./status-delete-dialog.component.css']
})
export class StatusDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public statusDeleteDialogRef: MatDialogRef<StatusDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public statusData: any,
    private toastr: ToastrService
  ) { }

  public deleteStatusSubscription: any;

  public btnDeleteStatus() {
    let btnDeleteStatus: Element = document.getElementById("btnDeleteStatus");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteStatus.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteStatus(this.statusData.objStatusModel.Id);
    this.deleteStatusSubscription = this.systemTablesService.deleteStatusObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Status was successfully deleted.", "Success");
          this.statusDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteStatus.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteStatusSubscription != null) this.deleteStatusSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.statusDeleteDialogRef.close();

    if (this.deleteStatusSubscription != null) this.deleteStatusSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteStatusSubscription != null) this.deleteStatusSubscription.unsubscribe();
  }

}
