import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TypeOfCitizenshipModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-type-of-citizenship-detail-dialog',
  templateUrl: './type-of-citizenship-detail-dialog.component.html',
  styleUrls: ['./type-of-citizenship-detail-dialog.component.css']
})
export class TypeOfCitizenshipDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public typeOfCitizenshipDetailDialogRef: MatDialogRef<TypeOfCitizenshipDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public typeOfCitizenshipData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Citizenship Type Detail";

  public typeOfCitizenshipModel: TypeOfCitizenshipModel = {
    Id: this.typeOfCitizenshipData.objTypeOfCitizenshipModel.Id,
    TypeOfCitizenship: this.typeOfCitizenshipData.objTypeOfCitizenshipModel.TypeOfCitizenship
  }

  public saveTypeOfCitizenshipSubscription: any;

  public btnSaveTypeOfCitizenship(): void {
    let btnSaveTypeOfCitizenship: Element = document.getElementById("btnSaveTypeOfCitizenship");
    let btnCloseTypeOfCitizenshipDialog: Element = document.getElementById("btnCloseTypeOfCitizenshipDialog");

    btnSaveTypeOfCitizenship.setAttribute("disabled", "disabled");
    btnCloseTypeOfCitizenshipDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveTypeOfCitizenship(this.typeOfCitizenshipModel);
    this.saveTypeOfCitizenshipSubscription = this.systemTablesService.saveTypeOfCitizenshipObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Citizenship type was successfully saved.", "Success");
          this.typeOfCitizenshipDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveTypeOfCitizenship.removeAttribute("disabled");
          btnCloseTypeOfCitizenshipDialog.removeAttribute("disabled");
        }

        if (this.saveTypeOfCitizenshipSubscription != null) this.saveTypeOfCitizenshipSubscription.unsubscribe();
      }
    );
  }

  public btnCloseTypeOfCitizenshipDialog(): void {
    this.typeOfCitizenshipDetailDialogRef.close();

    if (this.saveTypeOfCitizenshipSubscription != null) this.saveTypeOfCitizenshipSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.typeOfCitizenshipData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveTypeOfCitizenshipSubscription != null) this.saveTypeOfCitizenshipSubscription.unsubscribe();
  }

}
