import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

import { UserService } from './user.service';
import { UserModel } from './user.model';

import { UserDetailDialogComponent } from './user-detail-dialog/user-detail-dialog.component';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  constructor(
    private userService: UserService,
    public userDetailDialog: MatDialog
  ) { }

  public listUserSubscription: any;
  public listUserObservableArray: ObservableArray = new ObservableArray();
  public listUserCollectionView: CollectionView = new CollectionView(this.listUserObservableArray);
  public listUserPageIndex: number = 15;
  public userModel: UserModel = new UserModel();

  public showFields: boolean = false;

  public listUserData(): void {
    this.listUserObservableArray = new ObservableArray();
    this.listUserCollectionView = new CollectionView(this.listUserObservableArray);
    this.listUserCollectionView.pageSize = 15;
    this.listUserCollectionView.trackChanges = true;
    this.listUserCollectionView.refresh();

    this.userService.listUser();
    this.listUserSubscription = this.userService.listUserObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listUserObservableArray = data;
          this.listUserCollectionView = new CollectionView(this.listUserObservableArray);
          this.listUserCollectionView.pageSize = this.listUserPageIndex;
          this.listUserCollectionView.trackChanges = true;
          this.listUserCollectionView.refresh();
        }

        if (this.listUserSubscription != null) this.listUserSubscription.unsubscribe();
      }
    );
  }

  public btnAddUsers(): void {
    this.showFields = true;

    this.userModel = new UserModel();

    const userDetailDialogRef = this.userDetailDialog.open(UserDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Add User",
        objUserModel: this.userModel,
        objUserShowFields: this.showFields
      },
      disableClose: true
    });

    userDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listUserData();
      }
    });
  }

  public btnEditUsers(): void {
    this.showFields = false;

    let currentEditItem = this.listUserCollectionView.currentItem;
    this.userModel.Id = currentEditItem.Id;
    this.userModel.UserName = currentEditItem.Username;
    this.userModel.Password = currentEditItem.Password;
    this.userModel.FullName = currentEditItem.Fullname;

    const userDetailDialogRef = this.userDetailDialog.open(UserDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Edit User",
        objUserModel: this.userModel,
        objUserShowFields: this.showFields
      },
      disableClose: true
    });

    userDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listUserData();
      }
    });
  }

  ngOnInit() {
    this.listUserData();
  }

  ngOnDestroy() {
    if (this.listUserSubscription != null) this.listUserSubscription.unsubscribe();
  }
}
