import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { EducationLevelModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-education-level-detail-dialog',
  templateUrl: './education-level-detail-dialog.component.html',
  styleUrls: ['./education-level-detail-dialog.component.css']
})
export class EducationLevelDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public educationLevelDetailDialogRef: MatDialogRef<EducationLevelDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public educationLevelData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Education Level Detail";

  public educationLevelModel: EducationLevelModel = {
    Id: this.educationLevelData.objEducationLevelModel.Id,
    EducationLevel: this.educationLevelData.objEducationLevelModel.EducationLevel
  }

  public saveEducationLevelSubscription: any;

  public btnSaveEducationLevel(): void {
    let btnSaveEducationLevel: Element = document.getElementById("btnSaveEducationLevel");
    let btnCloseEducationLevelDialog: Element = document.getElementById("btnCloseEducationLevelDialog");

    btnSaveEducationLevel.setAttribute("disabled", "disabled");
    btnCloseEducationLevelDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveEducationLevel(this.educationLevelModel);
    this.saveEducationLevelSubscription = this.systemTablesService.saveEducationLevelObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Education level was successfully saved.", "Success");
          this.educationLevelDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveEducationLevel.removeAttribute("disabled");
          btnCloseEducationLevelDialog.removeAttribute("disabled");
        }

        if (this.saveEducationLevelSubscription != null) this.saveEducationLevelSubscription.unsubscribe();
      }
    );
  }

  public btnCloseEducationLevelDialog(): void {
    this.educationLevelDetailDialogRef.close();

    if (this.saveEducationLevelSubscription != null) this.saveEducationLevelSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.educationLevelData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveEducationLevelSubscription != null) this.saveEducationLevelSubscription.unsubscribe();
  }

}
