import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ServiceModel } from '../services.model'
import { ServicesService } from '../services.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

@Component({
  selector: 'app-service-detail-dialog',
  templateUrl: './service-detail-dialog.component.html',
  styleUrls: ['./service-detail-dialog.component.css']
})
export class ServiceDetailDialogComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public serviceDetailDialogRef: MatDialogRef<ServiceDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Service  Detail";

  public serviceModel: ServiceModel = {
    Id: this.serviceData.objServiceModel.Id,
    Service: this.serviceData.objServiceModel.Service,
    Description: this.serviceData.objServiceModel.Description,
    ServiceDepartmentId: this.serviceData.objServiceModel.ServiceDepartmentId,
    ServiceGroupId: this.serviceData.objServiceModel.ServiceGroupId,
    DateEncoded: this.serviceData.objServiceModel.DateEncoded,
    DateExpiry: this.serviceData.objServiceModel.DateExpiry,
    LimitAmount: this.serviceData.objServiceModel.LimitAmount,
    IsMultipleUse: this.serviceData.objServiceModel.IsMultipleUse,
    StatusId: this.serviceData.objServiceModel.StatusId,
    IsLocked: this.serviceData.objServiceModel.IsLocked
  }

  public listDropdownServiceDepartmentSubscription: any;
  public listDropdownServiceDepartmentObservableArray: ObservableArray = new ObservableArray();
  public listDropdownServiceGroupSubscription: any;
  public listDropdownServiceGroupObservableArray: ObservableArray = new ObservableArray();
  public listDropdownStatusSubscription: any;
  public listDropdownStatusObservableArray: ObservableArray = new ObservableArray();

  public saveServiceSubscription: any;
  public lockServiceSubscription: any;
  public unlockServiceSubscription: any;

  public listDropdownServiceDepartment(): void {
    this.servicesService.listServiceDropdownServiceDepartment();
    this.listDropdownServiceDepartmentSubscription = this.servicesService.listServiceDropdownServiceDepartmentObservable.subscribe(
      data => {
        this.listDropdownServiceDepartmentObservableArray = data;
        this.listDropdownServiceGroup(this.serviceModel.ServiceDepartmentId);

        if (this.listDropdownServiceDepartmentSubscription != null) this.listDropdownServiceDepartmentSubscription.unsubscribe();
      }
    );
  }

  public listDropdownServiceGroup(serviceDepartmentId: number): void {
    this.servicesService.listServiceDropdownServiceGroup(serviceDepartmentId);
    this.listDropdownServiceGroupSubscription = this.servicesService.listServiceDropdownServiceGroupObservable.subscribe(
      data => {
        this.listDropdownServiceGroupObservableArray = data;
        this.listDropdownStatus();

        if (data.length <= 0) {
          this.serviceModel.ServiceGroupId = 0;
        }

        if (this.listDropdownServiceGroupSubscription != null) this.listDropdownServiceGroupSubscription.unsubscribe();
      }
    );
  }

  public listDropdownStatus(): void {
    this.servicesService.listDropdownStatus();
    this.listDropdownStatusSubscription = this.servicesService.listDropdownStatusObservable.subscribe(
      data => {
        this.listDropdownStatusObservableArray = data;
        if (this.listDropdownStatusSubscription != null) this.listDropdownStatusSubscription.unsubscribe();
      }
    );
  }

  public btnSaveService(): void {
    let btnSaveService: Element = document.getElementById("btnSaveService");
    let btnLockService: Element = document.getElementById("btnLockService");
    let btnCloseService: Element = document.getElementById("btnCloseService");

    btnSaveService.setAttribute("disabled", "disabled");
    btnLockService.setAttribute("disabled", "disabled");
    btnCloseService.setAttribute("disabled", "disabled");

    this.servicesService.saveService(this.serviceModel);
    this.saveServiceSubscription = this.servicesService.saveServiceObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Service was successfully saved.", "Success");
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
        }

        btnSaveService.removeAttribute("disabled");
        btnLockService.removeAttribute("disabled");
        btnCloseService.removeAttribute("disabled");

        if (this.saveServiceSubscription != null) this.saveServiceSubscription.unsubscribe();
      }
    );
  }

  public btnLockService(): void {
    let btnSaveService: Element = document.getElementById("btnSaveService");
    let btnLockService: Element = document.getElementById("btnLockService");
    let btnCloseService: Element = document.getElementById("btnCloseService");

    btnSaveService.setAttribute("disabled", "disabled");
    btnLockService.setAttribute("disabled", "disabled");
    btnCloseService.setAttribute("disabled", "disabled");

    this.servicesService.lockService(this.serviceModel);
    this.lockServiceSubscription = this.servicesService.lockServiceObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Service was successfully locked.", "Success");
          this.serviceModel.IsLocked = true;
          btnCloseService.removeAttribute("disabled");
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveService.removeAttribute("disabled");
          btnLockService.removeAttribute("disabled");
          btnCloseService.removeAttribute("disabled");
        }

        if (this.lockServiceSubscription != null) this.lockServiceSubscription.unsubscribe();
      }
    );
  }

  public btnUnlockService(): void {
    let btnSaveService: Element = document.getElementById("btnSaveService");
    let btnLockService: Element = document.getElementById("btnLockService");
    let btnUnlockService: Element = document.getElementById("btnUnlockService");
    let btnCloseService: Element = document.getElementById("btnCloseService");

    btnSaveService.setAttribute("disabled", "disabled");
    btnLockService.setAttribute("disabled", "disabled");
    btnUnlockService.setAttribute("disabled", "disabled");
    btnCloseService.setAttribute("disabled", "disabled");

    this.servicesService.unlockService(this.serviceModel);
    this.unlockServiceSubscription = this.servicesService.unlockServiceObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Service was successfully unlocked.", "Success");
          this.serviceModel.IsLocked = false;
          btnCloseService.removeAttribute("disabled");
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnUnlockService.removeAttribute("disabled");
          btnCloseService.removeAttribute("disabled");
        }

        if (this.unlockServiceSubscription != null) this.unlockServiceSubscription.unsubscribe();
      }
    );
  }

  public btnCloseService(): void {
    this.serviceDetailDialogRef.close(200);

    if (this.listDropdownServiceGroupSubscription != null) this.listDropdownServiceGroupSubscription.unsubscribe();
    if (this.listDropdownStatusSubscription != null) this.listDropdownStatusSubscription.unsubscribe();
    if (this.saveServiceSubscription != null) this.saveServiceSubscription.unsubscribe();
    if (this.lockServiceSubscription != null) this.lockServiceSubscription.unsubscribe();
    if (this.unlockServiceSubscription != null) this.unlockServiceSubscription.unsubscribe();
  }

  public serviceDepartmentModelChange(): void {
    this.listDropdownServiceGroup(this.serviceModel.ServiceDepartmentId);
  }

  ngOnInit() {
    this.listDropdownServiceDepartment();
    this.dialogTitle = this.serviceData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.listDropdownServiceGroupSubscription != null) this.listDropdownServiceGroupSubscription.unsubscribe();
    if (this.listDropdownStatusSubscription != null) this.listDropdownStatusSubscription.unsubscribe();
    if (this.saveServiceSubscription != null) this.saveServiceSubscription.unsubscribe();
    if (this.lockServiceSubscription != null) this.lockServiceSubscription.unsubscribe();
    if (this.unlockServiceSubscription != null) this.unlockServiceSubscription.unsubscribe();
  }

}
