import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { UserModel } from '../user.model'
import { UserService } from '../user.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-user-detail-dialog',
  templateUrl: './user-detail-dialog.component.html',
  styleUrls: ['./user-detail-dialog.component.css']
})
export class UserDetailDialogComponent implements OnInit {

  constructor(
    private userService: UserService,
    public userDetailDialogRef: MatDialogRef<UserDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public userData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "User Detail";

  public userModel: UserModel = {
    Id: this.userData.objUserModel.Id,
    UserName: this.userData.objUserModel.UserName,
    Password: this.userData.objUserModel.Password,
    ConfirmPassword: this.userData.objUserModel.ConfirmPassword,
    FullName: this.userData.objUserModel.FullName
  }

  public saveUserSubscription: any;
  public showFields: boolean = this.userData.objUserShowFields;

  public btnSaveUser(): void {
    let btnSaveUser: Element = document.getElementById("btnSaveUser");
    let btnCloseUserDialog: Element = document.getElementById("btnCloseUserDialog");

    btnSaveUser.setAttribute("disabled", "disabled");
    btnCloseUserDialog.setAttribute("disabled", "disabled");

    this.userService.saveUser(this.userModel);
    this.saveUserSubscription = this.userService.saveUserObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("User was successfully saved.", "Success");
          this.userDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveUser.removeAttribute("disabled");
          btnCloseUserDialog.removeAttribute("disabled");
        }

        if (this.saveUserSubscription != null) this.saveUserSubscription.unsubscribe();
      }
    );
  }

  public btnCloseUserDialog(): void {
    this.userDetailDialogRef.close();

    if (this.saveUserSubscription != null) this.saveUserSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.userData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveUserSubscription != null) this.saveUserSubscription.unsubscribe();
  }

}
