import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

import { CardService } from './card.service';
import { CardModel } from './card.model';

import { CardDetailDialogComponent } from './card-detail-dialog/card-detail-dialog.component';
import { CardDeleteDialogComponent } from './card-delete-dialog/card-delete-dialog.component';
import { CardCitizenDialogComponent } from './card-citizen-dialog/card-citizen-dialog.component';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.css']
})
export class CardComponent implements OnInit {

  constructor(
    private cardService: CardService,
    public cardDetailDialog: MatDialog,
    public cardDeleteDialog: MatDialog,
    public cardCitizenlDialog: MatDialog
  ) { }

  public listCardSubscription: any;
  public listCardObservableArray: ObservableArray = new ObservableArray();
  public listCardCollectionView: CollectionView = new CollectionView(this.listCardObservableArray);
  public listCardPageIndex: number = 15;
  public cardModel: CardModel = new CardModel();

  public listCardData(): void {
    this.listCardObservableArray = new ObservableArray();
    this.listCardCollectionView = new CollectionView(this.listCardObservableArray);
    this.listCardCollectionView.pageSize = 15;
    this.listCardCollectionView.trackChanges = true;
    this.listCardCollectionView.refresh();

    this.cardService.listCard();
    this.listCardSubscription = this.cardService.listCardObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listCardObservableArray = data;
          this.listCardCollectionView = new CollectionView(this.listCardObservableArray);
          this.listCardCollectionView.pageSize = this.listCardPageIndex;
          this.listCardCollectionView.trackChanges = true;
          this.listCardCollectionView.refresh();
        }

        if (this.listCardSubscription != null) this.listCardSubscription.unsubscribe();
      }
    );
  }

  public btnAddCard(): void {
    this.cardModel = new CardModel();

    const cardCitizenlDialogRef = this.cardCitizenlDialog.open(CardCitizenDialogComponent, {
      width: '1200px',
      disableClose: true
    });

    cardCitizenlDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listCardData();
      }
    });
  }

  public btnEditCard(): void {
    let currentEditItem = this.listCardCollectionView.currentItem;
    this.cardModel.Id = currentEditItem.Id;
    this.cardModel.CitizenId = currentEditItem.CitizenId;
    this.cardModel.Citizen = currentEditItem.Citizen;
    this.cardModel.CardNumber = currentEditItem.CardNumber;
    this.cardModel.TotalBalance = currentEditItem.TotalBalance;
    this.cardModel.StatusId = currentEditItem.StatusId;

    const cardDetailDialogRef = this.cardDetailDialog.open(CardDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Edit Card",
        objCardModel: this.cardModel
      },
      disableClose: true
    });

    cardDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listCardData();
      }
    });
  }

  public btnDeleteCard(): void {
    let currentEditItem = this.listCardCollectionView.currentItem;
    this.cardModel.Id = currentEditItem.Id;

    const cardDeleteDialogRef = this.cardDeleteDialog.open(CardDeleteDialogComponent, {
      width: '300px',
      data: { objCardModel: this.cardModel },
      disableClose: true
    });

    cardDeleteDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listCardData();
      }
    });
  }

  ngOnInit() {
    this.listCardData();
  }

}
