import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-civil-status-delete-dialog',
  templateUrl: './civil-status-delete-dialog.component.html',
  styleUrls: ['./civil-status-delete-dialog.component.css']
})
export class CivilStatusDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public civilStatusDeleteDialogRef: MatDialogRef<CivilStatusDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public civilStatusData: any,
    private toastr: ToastrService
  ) { }

  public deleteCivilStatusSubscription: any;

  public btnDeleteCivilStatus() {
    let btnDeleteCivilStatus: Element = document.getElementById("btnDeleteCivilStatus");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteCivilStatus.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteCivilStatus(this.civilStatusData.objCivilStatusModel.Id);
    this.deleteCivilStatusSubscription = this.systemTablesService.deleteCivilStatusObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Civil status was successfully deleted.", "Success");
          this.civilStatusDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteCivilStatus.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteCivilStatusSubscription != null) this.deleteCivilStatusSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.civilStatusDeleteDialogRef.close();

    if (this.deleteCivilStatusSubscription != null) this.deleteCivilStatusSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteCivilStatusSubscription != null) this.deleteCivilStatusSubscription.unsubscribe();
  }

}
