import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-city-delete-dialog',
  templateUrl: './city-delete-dialog.component.html',
  styleUrls: ['./city-delete-dialog.component.css']
})
export class CityDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public cityDeleteDialogRef: MatDialogRef<CityDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public cityData: any,
    private toastr: ToastrService
  ) { }

  public deleteCitySubscription: any;

  public btnDeleteCity() {
    let btnDeleteCity: Element = document.getElementById("btnDeleteCity");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteCity.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteCity(this.cityData.objCityModel.Id);
    this.deleteCitySubscription = this.systemTablesService.deleteCityObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("City was successfully deleted.", "Success");
          this.cityDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteCity.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteCitySubscription != null) this.deleteCitySubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.cityDeleteDialogRef.close();

    if (this.deleteCitySubscription != null) this.deleteCitySubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteCitySubscription != null) this.deleteCitySubscription.unsubscribe();
  }

}
