import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-occupation-delete-dialog',
  templateUrl: './occupation-delete-dialog.component.html',
  styleUrls: ['./occupation-delete-dialog.component.css']
})
export class OccupationDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public occupationDeleteDialogRef: MatDialogRef<OccupationDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public occupationData: any,
    private toastr: ToastrService
  ) { }

  public deleteOccupationSubscription: any;

  public btnDeleteOccupation() {
    let btnDeleteOccupation: Element = document.getElementById("btnDeleteOccupation");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteOccupation.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteOccupation(this.occupationData.objOccupationModel.Id);
    this.deleteOccupationSubscription = this.systemTablesService.deleteOccupationObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Occupation was successfully deleted.", "Success");
          this.occupationDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteOccupation.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteOccupationSubscription != null) this.deleteOccupationSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.occupationDeleteDialogRef.close();

    if (this.deleteOccupationSubscription != null) this.deleteOccupationSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteOccupationSubscription != null) this.deleteOccupationSubscription.unsubscribe();
  }

}
