import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CityModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

@Component({
  selector: 'app-city-detail-dialog',
  templateUrl: './city-detail-dialog.component.html',
  styleUrls: ['./city-detail-dialog.component.css']
})
export class CityDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public cityDetailDialogRef: MatDialogRef<CityDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public cityData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "City Detail";

  public cityModel: CityModel = {
    Id: this.cityData.objCityModel.Id,
    City: this.cityData.objCityModel.City,
    CountryId: this.cityData.objCityModel.CountryId,
    RegionId: this.cityData.objCityModel.RegionId,
    ProvinceId: this.cityData.objCityModel.ProvinceId
  }

  public listCityDropdownCountrySubscription: any;
  public listCityDropdownCountryObservableArray: ObservableArray = new ObservableArray();
  public listCityDropdownRegionSubscription: any;
  public listCityDropdownRegionObservableArray: ObservableArray = new ObservableArray();
  public listCityDropdownProvinceSubscription: any;
  public listCityDropdownProvinceObservableArray: ObservableArray = new ObservableArray();

  public saveCitySubscription: any;

  public listDropdownCountry(): void {
    this.systemTablesService.listCityDropdownCountry();
    this.listCityDropdownCountrySubscription = this.systemTablesService.listCityDropdownCountryObservable.subscribe(
      data => {
        this.listCityDropdownCountryObservableArray = data;
        this.listDropdownRegion(this.cityModel.CountryId);

        if (this.listCityDropdownCountrySubscription != null) this.listCityDropdownCountrySubscription.unsubscribe();
      }
    );
  }

  public listDropdownRegion(countryId: number): void {
    this.systemTablesService.listCityDropdownRegion(countryId);
    this.listCityDropdownRegionSubscription = this.systemTablesService.listCityDropdownRegionObservable.subscribe(
      data => {
        this.listCityDropdownRegionObservableArray = data;
        this.listDropdownProvince(this.cityModel.RegionId);

        if (data.length <= 0) {
          this.cityModel.RegionId = 0;
        }

        if (this.listCityDropdownRegionSubscription != null) this.listCityDropdownRegionSubscription.unsubscribe();
      }
    );
  }

  public listDropdownProvince(regionId: number): void {
    this.systemTablesService.listCityDropdownProvince(regionId);
    this.listCityDropdownProvinceSubscription = this.systemTablesService.listCityDropdownProvinceObservable.subscribe(
      data => {
        this.listCityDropdownProvinceObservableArray = data;

        if (data.length <= 0) {
          this.cityModel.ProvinceId = 0;
        }

        if (this.listCityDropdownProvinceSubscription != null) this.listCityDropdownProvinceSubscription.unsubscribe();
      }
    );
  }

  public btnSaveCity(): void {
    let btnSaveCity: Element = document.getElementById("btnSaveCity");
    let btnCloseCityDialog: Element = document.getElementById("btnCloseCityDialog");

    btnSaveCity.setAttribute("disabled", "disabled");
    btnCloseCityDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveCity(this.cityModel);
    this.saveCitySubscription = this.systemTablesService.saveCityObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("City was successfully saved.", "Success");
          this.cityDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveCity.removeAttribute("disabled");
          btnCloseCityDialog.removeAttribute("disabled");
        }

        if (this.saveCitySubscription != null) this.saveCitySubscription.unsubscribe();
      }
    );
  }

  public btnCloseCityDialog(): void {
    this.cityDetailDialogRef.close();

    if (this.listCityDropdownCountrySubscription != null) this.listCityDropdownCountrySubscription.unsubscribe();
    if (this.listCityDropdownRegionSubscription != null) this.listCityDropdownRegionSubscription.unsubscribe();
    if (this.listCityDropdownProvinceSubscription != null) this.listCityDropdownProvinceSubscription.unsubscribe();
    if (this.saveCitySubscription != null) this.saveCitySubscription.unsubscribe();
  }

  public countryModelChange(): void {
    this.listDropdownRegion(this.cityModel.CountryId);
  }

  public regionModelChange(): void {
    this.listDropdownProvince(this.cityModel.RegionId);
  }

  ngOnInit() {
    this.dialogTitle = this.cityData.objDialogTitle;
    this.listDropdownCountry();
  }

  ngOnDestroy() {
    if (this.listCityDropdownCountrySubscription != null) this.listCityDropdownCountrySubscription.unsubscribe();
    if (this.listCityDropdownRegionSubscription != null) this.listCityDropdownRegionSubscription.unsubscribe();
    if (this.listCityDropdownProvinceSubscription != null) this.listCityDropdownProvinceSubscription.unsubscribe();
    if (this.saveCitySubscription != null) this.saveCitySubscription.unsubscribe();
  }

}
