import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ServicesService } from '../services.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-service-delete-dialog',
  templateUrl: './service-delete-dialog.component.html',
  styleUrls: ['./service-delete-dialog.component.css']
})
export class ServiceDeleteDialogComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public serviceDeleteDialogRef: MatDialogRef<ServiceDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceData: any,
    private toastr: ToastrService
  ) { }

  public deleteServiceSubscription: any;

  public btnDeleteService() {
    let btnDeleteService: Element = document.getElementById("btnDeleteService");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteService.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.servicesService.deleteService(this.serviceData.objServiceModel.Id);
    this.deleteServiceSubscription = this.servicesService.deleteServiceObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Service was successfully deleted.", "Success");
          this.serviceDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteService.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteServiceSubscription != null) this.deleteServiceSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.serviceDeleteDialogRef.close();

    if (this.deleteServiceSubscription != null) this.deleteServiceSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteServiceSubscription != null) this.deleteServiceSubscription.unsubscribe();
  }

}
