import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BarangayModel } from '../system-tables.model'
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

@Component({
  selector: 'app-barangay-detail-dialog',
  templateUrl: './barangay-detail-dialog.component.html',
  styleUrls: ['./barangay-detail-dialog.component.css']
})
export class BarangayDetailDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public barangayDetailDialogRef: MatDialogRef<BarangayDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public barangayData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Barangay Detail";

  public barangayModel: BarangayModel = {
    Id: this.barangayData.objBarangayModel.Id,
    Barangay: this.barangayData.objBarangayModel.Barangay,
    BarangayChairman: this.barangayData.objBarangayModel.BarangayChairman,
    CountryId: this.barangayData.objBarangayModel.CountryId,
    RegionId: this.barangayData.objBarangayModel.RegionId,
    ProvinceId: this.barangayData.objBarangayModel.ProvinceId,
    CityId: this.barangayData.objBarangayModel.CityId
  }

  public listBarangayDropdownCountrySubscription: any;
  public listBarangayDropdownCountryObservableArray: ObservableArray = new ObservableArray();
  public listBarangayDropdownRegionSubscription: any;
  public listBarangayDropdownRegionObservableArray: ObservableArray = new ObservableArray();
  public listBarangayDropdownProvinceSubscription: any;
  public listBarangayDropdownProvinceObservableArray: ObservableArray = new ObservableArray();
  public listBarangayDropdownCitySubscription: any;
  public listBarangayDropdownCityObservableArray: ObservableArray = new ObservableArray();

  public saveBarangaySubscription: any;

  public listDropdownCountry(): void {
    this.systemTablesService.listBarangayDropdownCountry();
    this.listBarangayDropdownCountrySubscription = this.systemTablesService.listBarangayDropdownCountryObservable.subscribe(
      data => {
        this.listBarangayDropdownCountryObservableArray = data;
        this.listDropdownRegion(this.barangayModel.CountryId);

        if (this.listBarangayDropdownCountrySubscription != null) this.listBarangayDropdownCountrySubscription.unsubscribe();
      }
    );
  }

  public listDropdownRegion(countryId: number): void {
    this.systemTablesService.listBarangayDropdownRegion(countryId);
    this.listBarangayDropdownRegionSubscription = this.systemTablesService.listBarangayDropdownRegionObservable.subscribe(
      data => {
        this.listBarangayDropdownRegionObservableArray = data;
        this.listDropdownProvince(this.barangayModel.RegionId);

        if (data.length <= 0) {
          this.barangayModel.RegionId = 0;
        }

        if (this.listBarangayDropdownRegionSubscription != null) this.listBarangayDropdownRegionSubscription.unsubscribe();
      }
    );
  }

  public listDropdownProvince(regionId: number): void {
    this.systemTablesService.listBarangayDropdownProvince(regionId);
    this.listBarangayDropdownProvinceSubscription = this.systemTablesService.listBarangayDropdownProvinceObservable.subscribe(
      data => {
        this.listBarangayDropdownProvinceObservableArray = data;
        this.listDropdownCity(this.barangayModel.ProvinceId);

        if (data.length <= 0) {
          this.barangayModel.ProvinceId = 0;
        }

        if (this.listBarangayDropdownProvinceSubscription != null) this.listBarangayDropdownProvinceSubscription.unsubscribe();
      }
    );
  }

  public listDropdownCity(provinceId: number): void {
    this.systemTablesService.listBarangayDropdownCity(provinceId);
    this.listBarangayDropdownCitySubscription = this.systemTablesService.listBarangayDropdownCityObservable.subscribe(
      data => {
        this.listBarangayDropdownCityObservableArray = data;

        if (data.length <= 0) {
          this.barangayModel.CityId = 0;
        }

        if (this.listBarangayDropdownCitySubscription != null) this.listBarangayDropdownCitySubscription.unsubscribe();
      }
    );
  }

  public btnSaveBarangay(): void {
    let btnSaveBarangay: Element = document.getElementById("btnSaveBarangay");
    let btnCloseBarangayDialog: Element = document.getElementById("btnCloseBarangayDialog");

    btnSaveBarangay.setAttribute("disabled", "disabled");
    btnCloseBarangayDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.saveBarangay(this.barangayModel);
    this.saveBarangaySubscription = this.systemTablesService.saveBarangayObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Barangay was successfully saved.", "Success");
          this.barangayDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveBarangay.removeAttribute("disabled");
          btnCloseBarangayDialog.removeAttribute("disabled");
        }

        if (this.saveBarangaySubscription != null) this.saveBarangaySubscription.unsubscribe();
      }
    );
  }

  public btnCloseBarangayDialog(): void {
    this.barangayDetailDialogRef.close();

    if (this.listBarangayDropdownCountrySubscription != null) this.listBarangayDropdownCountrySubscription.unsubscribe();
    if (this.listBarangayDropdownRegionSubscription != null) this.listBarangayDropdownRegionSubscription.unsubscribe();
    if (this.listBarangayDropdownProvinceSubscription != null) this.listBarangayDropdownProvinceSubscription.unsubscribe();
    if (this.listBarangayDropdownCitySubscription != null) this.listBarangayDropdownCitySubscription.unsubscribe();
    if (this.saveBarangaySubscription != null) this.saveBarangaySubscription.unsubscribe();
  }

  public countryModelChange(): void {
    this.listDropdownRegion(this.barangayModel.CountryId);
  }

  public regionModelChange(): void {
    this.listDropdownProvince(this.barangayModel.RegionId);
  }

  public provinceModelChange(): void {
    this.listDropdownCity(this.barangayModel.ProvinceId);
  }

  ngOnInit() {
    this.dialogTitle = this.barangayData.objDialogTitle;
    this.listDropdownCountry();
  }

  ngOnDestroy() {
    if (this.listBarangayDropdownCountrySubscription != null) this.listBarangayDropdownCountrySubscription.unsubscribe();
    if (this.listBarangayDropdownRegionSubscription != null) this.listBarangayDropdownRegionSubscription.unsubscribe();
    if (this.listBarangayDropdownProvinceSubscription != null) this.listBarangayDropdownProvinceSubscription.unsubscribe();
    if (this.listBarangayDropdownCitySubscription != null) this.listBarangayDropdownCitySubscription.unsubscribe();
    if (this.saveBarangaySubscription != null) this.saveBarangaySubscription.unsubscribe();
  }

}
