import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SystemTablesService } from '../system-tables.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-type-of-citizenship-delete-dialog',
  templateUrl: './type-of-citizenship-delete-dialog.component.html',
  styleUrls: ['./type-of-citizenship-delete-dialog.component.css']
})
export class TypeOfCitizenshipDeleteDialogComponent implements OnInit {

  constructor(
    private systemTablesService: SystemTablesService,
    public typeOfTypeOfCitizenshipDeleteDialogRef: MatDialogRef<TypeOfCitizenshipDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public typeOfCitizenshipData: any,
    private toastr: ToastrService
  ) { }

  public deleteTypeOfCitizenshipSubscription: any;

  public btnDeleteTypeOfCitizenship() {
    let btnDeleteTypeOfCitizenship: Element = document.getElementById("btnDeleteTypeOfCitizenship");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteTypeOfCitizenship.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.systemTablesService.deleteTypeOfCitizenship(this.typeOfCitizenshipData.objTypeOfCitizenshipModel.Id);
    this.deleteTypeOfCitizenshipSubscription = this.systemTablesService.deleteTypeOfCitizenshipObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Citizenship type was successfully deleted.", "Success");
          this.typeOfTypeOfCitizenshipDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteTypeOfCitizenship.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteTypeOfCitizenshipSubscription != null) this.deleteTypeOfCitizenshipSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.typeOfTypeOfCitizenshipDeleteDialogRef.close();

    if (this.deleteTypeOfCitizenshipSubscription != null) this.deleteTypeOfCitizenshipSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteTypeOfCitizenshipSubscription != null) this.deleteTypeOfCitizenshipSubscription.unsubscribe();
  }


}
