import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CitizensChildrenModel } from '../citizen-children-detail-dialog/citizen-children.model';
import { CitizenService } from '../../citizen.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-citizen-children-delete-dialog',
  templateUrl: './citizen-children-delete-dialog.component.html',
  styleUrls: ['./citizen-children-delete-dialog.component.css']
})
export class CitizenChildrenDeleteDialogComponent implements OnInit {

  constructor(
    private citizenService: CitizenService,
    public citizenChildrenDeleteDialogRef: MatDialogRef<CitizenChildrenDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public childData: any,
    private toastr: ToastrService
  ) { }

  public deleteCitizensChildrenSubscription: any;

  public btnDeleteChildren() {
    let btnDeleteChildren: Element = document.getElementById("btnDeleteChildren");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteChildren.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.citizenService.deleteCitizensChildren(this.childData.objCitizensChildrenModel.Id);
    this.deleteCitizensChildrenSubscription = this.citizenService.deleteCitizensChildrenObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Child was successfully deleted.", "Success");
          this.citizenChildrenDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteChildren.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteCitizensChildrenSubscription != null) this.deleteCitizensChildrenSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.citizenChildrenDeleteDialogRef.close();

    if (this.deleteCitizensChildrenSubscription != null) this.deleteCitizensChildrenSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteCitizensChildrenSubscription != null) this.deleteCitizensChildrenSubscription.unsubscribe();
  }
}
