import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ServicesService } from '../services.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-service-department-delete-dialog',
  templateUrl: './service-department-delete-dialog.component.html',
  styleUrls: ['./service-department-delete-dialog.component.css']
})
export class ServiceDepartmentDeleteDialogComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public serviceDepartmentDeleteDialogRef: MatDialogRef<ServiceDepartmentDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceDepartmentData: any,
    private toastr: ToastrService
  ) { }

  public deleteServiceDepartmentSubscription: any;

  public btnDeleteServiceDepartment() {
    let btnDeleteServiceDepartment: Element = document.getElementById("btnDeleteServiceDepartment");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteServiceDepartment.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.servicesService.deleteServiceDepartment(this.serviceDepartmentData.objServiceDepartmentModel.Id);
    this.deleteServiceDepartmentSubscription = this.servicesService.deleteServiceDepartmentObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Service department was successfully deleted.", "Success");
          this.serviceDepartmentDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteServiceDepartment.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteServiceDepartmentSubscription != null) this.deleteServiceDepartmentSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.serviceDepartmentDeleteDialogRef.close();

    if (this.deleteServiceDepartmentSubscription != null) this.deleteServiceDepartmentSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteServiceDepartmentSubscription != null) this.deleteServiceDepartmentSubscription.unsubscribe();
  }

}
